import React, { useState, useEffect, useContext } from "react";
import { MyGroupService } from '../../service/myGroup';
import AddGroupDialog from './addGroup';
import DeleteGroupDialog from './deleteGroup';
import StyledButton from '../common/buttons/styledButton';
import styled from 'styled-components';
import GroupRow from './groupRow';
import { AppContext } from '../../context/appContext';
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import Tooltip from '../common/tooltip/index';
import LoadingComponent from '../common/loading/index';

const Container = styled.div`
width: 100%;
min-height: 600px;
`;

const IconContainer = styled.div`
display: flex;
align-items: center;
padding: 4px;
&:hover {
  background-color: #E0E0E0;
}
`;

const TitleBar = styled.div`
background-color: #F0F0F0;
padding: 10px 10px 10px 4px;
display: flex;
justify-content: space-between;
align-items: center;
border-bottom: 1px solid #C0C0C0;
`;

const Title = styled.div`
  font-size: 24px;
  font-weight: bold;
  color: #C85050;
  margin-bottom: 6px;
`;

const MyGroupComponent = (props) => {  
  const { CurrentUser, dispatch } = useContext(AppContext);

  const [state, setState] = useState({
    groups: [], 
    loading: false, 
    editMode: false, 
    addGroupOpen: false, 
    deleteGroupOpen: false,
    selectedItem: null,
    sortASC: true,
    activeGroup: null,
  });

  useEffect(() => {
    loadGroup(null);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [CurrentUser, state.sortASC]);

  const loadGroup = (newGroup) => {
    if (CurrentUser != null) {
      setState({...state, loading: true, addGroupOpen: false, deleteGroupOpen: false });
      MyGroupService.loadGroups(CurrentUser.token, state.sortASC).then(res => {
        setState({...state, loading: false, addGroupOpen: false, deleteGroupOpen: false, groups: res.data });
        if (res.data) {
          if (newGroup !== null && res.data.find(p => p.id === newGroup.id)) {
            props.onSelected(newGroup);
          }
          else if (state.activeGroup === null) {
            props.onSelected(res.data[0]);            
          }
          else if (!res.data.find(p => p.id === state.activeGroup.id)) {
            props.onSelected(res.data[0]);
          }
        }
      });
    }
  }

  const onAddGroup = (item) => {
    if (!item || !CurrentUser) return;

    setState({...state, addGroupOpen: false });

      if (state.editMode) {
        MyGroupService.updateGroup(item.id, item.name, item.description, CurrentUser.token).then(res => {
          dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `Group: ${item.name} is updated`}});
          loadGroup(null);
        }, err => {
          dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to update group: ${item.name}`}});
        });
      } else {
        MyGroupService.addGroup(item.name, item.description, CurrentUser.token).then(res => {
          dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `Group: ${item.name} is added`}});
          loadGroup(res.data);
        }, err => {
          dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to add group: ${item.name}`}});
        });
      }
  }

  const onDeleteGroup = (item) => {
    if (!state.selectedItem) return;

    setState({...state, deleteGroupOpen: false });
    MyGroupService.deleteGroup(state.selectedItem.id, CurrentUser.token).then(res => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `Group: ${state.selectedItem.name} is deleted`}});
      loadGroup(null);
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to delete group: ${state.selectedItem.name}`}});
    });
  }

  const setActiveGroup = (item) => {
    setState({...state, activeGroup: item })
    props.onSelected(item);
  }

  return (
    <Container>
      <TitleBar>
        <div style={{display: 'flex', alignItems: 'center', marginLeft: '10px'}}>
          <Title>Groups</Title>
          <Tooltip placement="top" tooltip="Sort groups">
            <StyledButton 
              style={{ backgroundColor: '#1976D2', 
                      color: '#FFFFFF', 
                      fontSize: '12px',
                      padding: '2px',
                      margin: '0px 0px 0px 20px' }}
              onClick={() => setState({...state, sortASC: !state.sortASC})}
            >
              {state.sortASC && <ArrowDropUpIcon style={{color: '#FFFFFF', width: '18px', height: '18px' }}/>}
              {!state.sortASC && <ArrowDropDownIcon style={{color: '#FFFFFF', width: '18px', height: '18px' }}/>}
            </StyledButton>         
          </Tooltip>
          <Tooltip placement="top" tooltip="Add new group">
            <StyledButton 
              style={{ backgroundColor: '#3C9F40', 
                      color: '#FFFFFF', 
                      fontSize: '12px',
                      padding: '2px',
                      margin: '0px 0px 0px 20px' }}
              onClick={() => setState({...state, editMode: false, addGroupOpen: true, selectedItem: null})}
            >
              <AddIcon style={{color: '#FFFFFF', width: '18px', height: '18px' }}/>
            </StyledButton>         
          </Tooltip>
        </div>
        <IconContainer onClick={() => props.onCloseGroup()}>
            <CloseIcon style={{color: '#444444', width: '24px', height: '24px'}} />
        </IconContainer>
      </TitleBar>
      <div style={{position: 'relative', padding: '10px'}}>
        {state.groups.map((item, index) => {
          return <GroupRow
                  key={index}
                  lastRow={index === (state.groups.length - 1)}
                  item={item}
                  index={index}
                  onEdit={(item) => setState(Object.assign({}, state, {editMode: true, addGroupOpen: true, selectedItem: item}))} 
                  onDelete={(item) => setState({...state, deleteGroupOpen: true, selectedItem: item })} 
                  onSelected={setActiveGroup} 
          />;
        })}
        {state.loading && <LoadingComponent /> }
      </div>

      <AddGroupDialog
      item={state.selectedItem}
      editMode={state.editMode}
      open={state.addGroupOpen}
      close={() => setState({...state, addGroupOpen: false })}
      onAddGroup={(item) => onAddGroup(item)}
      />
    <DeleteGroupDialog 
      item={state.selectedItem}
      open={state.deleteGroupOpen}
      close={() => setState({...state, deleteGroupOpen: false })}
      onDelete={() => onDeleteGroup() } />

    </Container>
  )
}

export default MyGroupComponent;