import React, { useEffect, useState, useContext } from "react";
import Button from '@material-ui/core/Button';
import FavoriteIcon from '@material-ui/icons/Favorite';
import AddIcon from '@material-ui/icons/Add';
import { StockService } from '../../../service/stock';
import { MyStockService } from '../../../service/myStock';
import HistoricalChart from './historicalChart';
import ClickHandler from '../../common/clickHandler';
import { AppContext } from '../../../context/appContext';
import { GetLinkUrl } from '../dialog/webSiteLinks/index';
import Avatar from '@material-ui/core/Avatar';
import Tooltip from '../../common/tooltip/index';

function debounce(fn, ms) {
  let timer
  return _ => {
    clearTimeout(timer)
    timer = setTimeout(_ => {
      timer = null
      fn.apply(this, arguments)
    }, ms)
  };
}

const StockChartComponent = (props) => {  
  const {item} = props;
  const { name, id } = item;
  const { dispatch, StockLinkType, CurrentUser } = useContext(AppContext);

  const myRef = React.createRef(null);

  const [stockData, setStockData] = useState(null);
  const [dimensions, setDimensions] = React.useState({ 
    width: window.innerWidth
  })

  React.useEffect(() => {
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        width: myRef.current ? myRef.current.offsetWidth : window.innerWidth,
      });
    }, 1000)

    window.addEventListener('resize', debouncedHandleResize)

    return _ => {
      window.removeEventListener('resize', debouncedHandleResize)
    }
  });

  useEffect(() => {
    StockService.getHistoryData(name).then(res => {
      setStockData(res.data);
    })
  }, [name])

  const onRowClick = (name) => {
    dispatch({type: 'SET_SELECTED_SYMBOL', data: name});
    dispatch({type: 'SET_ACTIVE_SYMBOL', data: name});
  }

  const onRowDbClick = (name) => {
    dispatch({type: 'SET_ACTIVE_SYMBOL', data: name});
    window.open(GetLinkUrl(StockLinkType, name), "_Blank")
  }

  const addToMyFavorite = () => {
    if (CurrentUser) {
      MyStockService.addToMyFavorite(CurrentUser.token, id).then(res => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `${name} is added to group: My Favorite`}});
      }, err => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to add ${name} to group: My Favorite`}});
      });      
    }
  }

  return (
    <div ref={myRef} style={{width: '100%', backgroundColor: '#eeeeee', marginBottom: '10px', minHeight: '100px'}}>
      <div style={{display: 'flex', justifyContent: 'center'}}>
          <Button variant="contained" color="primary" onClick={ClickHandler(onRowClick, onRowDbClick, name)}>
            {name}
          </Button>
          <Tooltip placement="bottom" tooltip="Add to my favorite">
            <Avatar 
              style={{ backgroundColor: '#FF0000', 
                      width: '24px',
                      height: '24px',
                      padding: '2px',
                      cursor: 'pointer',
                      margin: '0px 20px 0px 20px' }}
              onClick={(e) => {e.stopPropagation(); addToMyFavorite()} }
            >
              <FavoriteIcon style={{color: '#FFFFFF', width: '16px', height: '16px'}} />
            </Avatar>         
          </Tooltip>
          <Tooltip placement="bottom" tooltip="Add to my group">
            <Avatar 
              style={{ backgroundColor: '#3C9F40', 
                      width: '24px',
                      height: '24px',
                      padding: '2px',
                      cursor: 'pointer',
                    }}
              onClick={(e) => {e.stopPropagation(); dispatch({type:'SYMBOL_TO_ADD', data: item})} }
            >
              <AddIcon style={{color: '#FFFFFF', width: '24px', height: '24px' }}/>
            </Avatar>         
          </Tooltip>
        </div>

      <HistoricalChart data={stockData} width={dimensions.width} />
    </div>
  )
}

export default StockChartComponent;