import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import MenuIcon from '@material-ui/icons/Menu';
import MyGroupComponent from '../myGroup/index';
import MyStockComponent from '../myStock/index';

const Container = styled.div`
background-color: #F1F1F1;
width: 100%;
min-height: 800px;
display: flex;
justify-content: space-between;
position: relative;
`;

const LeftPanel = styled.div`
height: 100%;
overflow: hidden;
position: relative;
flex-grow: 0;
flex-shrink: 0;
flex-basis: ${props => props.width + 'px' };
transition: flex-basis 0.5s;
`;

const RightPanel = styled.div`
position: relative;
flex: 1 1 300px;
background-color: #E0E0E0;
`;

const IconContainer = styled.div`
cursor: pointer;
position: absolute;
left: 0;
top: 0;
display: flex;
align-items: center;
padding: 8px 12px 8px 8px;
background-color: #222222;
&:hover {
  background-color: #444444;
}
`;

const MyCollection = (props) => {  
  const [width, setWidth] = useState(280);
  const [activeGroup, setActiveGroup] = useState({id: 0, name: ''});

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const toggleSideBar = () => {
    setWidth((prev) => prev !== 0 ? 0 : 280);
  }

  return (
    <Container> 
        <LeftPanel width={width}>
          <MyGroupComponent onSelected={(item) => setActiveGroup(item)} onCloseGroup={() => toggleSideBar()} />
        </LeftPanel>
        <RightPanel style={{width: '300px'}}>
          <MyStockComponent id={activeGroup.id} name={activeGroup.name} />
           { width === 0 &&
           <IconContainer onClick={() => toggleSideBar()}>
              <MenuIcon style={{color: '#FFFFFF', width: '20px', height: '20px'}} />
            </IconContainer>
            }
        </RightPanel>
    </Container>
  )
}

export default MyCollection;