import React, { useContext } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Tooltip from '../tooltip/index';
import { AppContext } from '../../../context/appContext';
import DeleteIcon from '@material-ui/icons/Delete';

const SymbolDeleteCell = ({item, groupId, mouseOver}) => {
  const { CurrentUser, dispatch } = useContext(AppContext);
  const { id, name } = item;

  return (CurrentUser ? 
    (<div>
      <span>{name}</span>
      {mouseOver && 
        <div style={{display: 'flex'}}>
          <Tooltip placement="bottom" tooltip="Remove this symbol">
            <Avatar 
              style={{ backgroundColor: '#FF0000', 
                      width: '24px',
                      height: '24px',
                      padding: '2px',
                      margin: '0px 10px 0px 0px' }}
              onClick={(e) => {e.stopPropagation(); dispatch({type:'SET_DELETE_SYMBOL', data: {id, name, groupId}})} }
            >
              <DeleteIcon style={{color: '#FFFFFF', width: '16px', height: '16px'}} />
            </Avatar>         
          </Tooltip>
        </div>
      }
    </div>
    )
    :
    (<div><span>{name}</span></div>)
  )
}

export default SymbolDeleteCell;