
import axios from 'axios';
import ServerUrl from "./server";

export const MyStockService = {
    loadGroupStock,
    addToMyFavorite,
    addToGroup,
    addToGroupByName,
    deleteFromGroup,
};

function loadGroupStock(token, groupId, start, pageSize, sortMethod) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyStocks/${groupId}/${start}/${pageSize}/${sortMethod}`,
    headers: {'Authorization': "bearer " + token}
  });
}

function addToMyFavorite(token, stockId) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyGroups/AddToFavorite/${stockId}`,
    headers: {'Authorization': "bearer " + token}
  });
}

function addToGroup(token, groupId, stockId) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyGroups/AddToGroup/${groupId}/${stockId}`,
    headers: {'Authorization': "bearer " + token}
  });
}

function addToGroupByName(token, groupId, name) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyStocks/add/${groupId}/${name}`,
    headers: {'Authorization': "bearer " + token}
  });
}

function deleteFromGroup(token, groupId, stockId) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyGroups/DeleteFromGroup/${groupId}/${stockId}`,
    headers: {'Authorization': "bearer " + token}
  });
}

