import React, { useState, useContext, useEffect } from "react";
import { Paper, Divider, LinearProgress, Typography, Box } from '@material-ui/core';
import { Button } from "@material-ui/core";
import UploadIcon from '@material-ui/icons/CloudUpload';
import { withStyles } from '@material-ui/core/styles';
import Styled from 'styled-components';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';
import * as signalR from "@microsoft/signalr";

const Container = Styled.div`
padding: 20px;
`;

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const UpdateGroupComponent = () => {
  const { dispatch, CurrentUser } = useContext(AppContext);
  const [uploadFile, setUploadFile] = useState(null);
  const [groupName, setGroupName] = useState('');
  const [progress, setProgress] = useState(null);

  useEffect(() => {
    const hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(`messagehub`)
      .withAutomaticReconnect()
      .build();

    hubConnection.start().then(() => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: "SignalR connected"}});
      if (hubConnection.connectionId) {
        hubConnection.invoke("sendConnectionId", hubConnection.connectionId);
      }   
      hubConnection.on('updateGroupProgress', (message) => {
        setProgress(parseInt(message));
      });
    }).catch(err => { });  

    return () => {
      hubConnection.stop().then(() => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'warning', text: "SignalR disconnected"}});
      });
    }
  }, [dispatch]);


  const ProgressComponent = () => {
    return (
      <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
        <Box width="100%" mr={1}>
          <BorderLinearProgress variant="determinate" value={progress} />
        </Box>
        <Box minWidth={35}>
          <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
        </Box>
      </Box>
    );
  };

  const startUploadFile = (e) => {
    e.preventDefault();
    if (uploadFile === null) return;

    const formData = new FormData();
    formData.append('Name', groupName);
    formData.append('File', uploadFile);

    StockService.updateGroup(formData, CurrentUser.token);
  }

  return (
    <div style={{width: '80%', margin: '10px 10% 30px 10%'}}>
    <Paper elevation={4} style={{borderRadius:'10px', margin:'10px 0px'}}>
      <Container>
        <div style={{fontSize:'24px', fontWeight:'bold'}}>Update Group {groupName}</div>
      </Container>
      <Divider />
      <Container>
        <form onSubmit={e => startUploadFile(e)}>
          <div style={{display: 'flex', alignItems:'center'}}>
            <div style={{paddingLeft:'10px', marginRight:'30px'}}>
              <input type="text" onChange={e => setGroupName(e.target.value)} style={{padding:'10px'}}/>
            </div>
            <div style={{paddingLeft:'10px'}}>
              <input type="file" onChange={e => setUploadFile(e.target.files[0])} />
            </div>
            <Button type="submit"
              variant="contained"
              color="primary"
              startIcon={<UploadIcon />}
              style={{marginRight:'30px'}}
            >
              Upload
            </Button>
          </div>
        </form>
        {progress && progress >= 0 && <ProgressComponent />}
      </Container>
    </Paper>
    </div>
  );
};
 
export default UpdateGroupComponent;