
export const appReducer = (state, action) => {
    switch (action.type) {
      case 'SET_STOCK_LINKTYPE':
        return Object.assign({}, state, {StockLinkType: action.data})
      case 'SET_ACTIVE_SYMBOL':
        return Object.assign({}, state, {ActiveStock: action.data})
      case 'SET_SELECTED_SYMBOL':
        return Object.assign({}, state, {StockSelected: action.data})  
      case 'SET_DELETE_SYMBOL':
        return Object.assign({}, state, {StockToDelete: action.data})  
      case 'SYMBOL_REMOVED':
        return Object.assign({}, state, {GroupStockUpdate: state.GroupStockUpdate + 1})  
      case 'SET_NEWSTOCK_GROUP':
        return Object.assign({}, state, {GroupForNewStock: action.data})    
      case 'SYMBOL_TO_ADD':
        return Object.assign({}, state, {StockToAdd: action.data})
      case 'SAVED_PATH':
        return Object.assign({}, state, {SavedPath: action.data});
      case 'LOGIN':
        return Object.assign({}, state, {CurrentUser: action.data});
      case 'LOGOUT':
        return Object.assign({}, state, {CurrentUser: null});
      case 'INC_COUNT':
        let incState = Object.assign({}, state);
        incState.count = state.count + 1;
        return incState;
      case 'DEC_COUNT':
        let decState = Object.assign({}, state);
        decState.count = state.count - 1;
        return decState;
      case 'SET_UPDATESYMBOL':
        return Object.assign({}, state, {UpdateSymbol: action.data});
      case 'SET_UPDATESYMBOLPROGRESS':
        return Object.assign({}, state, {UpdateSymbolProgress: action.data});
      case 'SET_PROJECTS':
        return Object.assign({}, state, {PP_ProjectList_EE: action.data});
      case 'ADD_MESSAGE':
        let timeId = new Date().getTime();
        let newState = Object.assign({}, state);
        newState.Messages.push({id: timeId, message: action.data});
        return newState;
      case 'REMOVE_MESSAGES':
        let removeState = Object.assign({}, state);  
        removeState.Messages = removeState.Messages.filter(p => !action.data.includes(p.id))
        return removeState;        
      default:
        return state;
    }
  } 