import React, { useContext } from "react";
import MessageBar from '../common/messagebar/index';
import DialogPool from './dialogPool';
import { Link } from "react-router-dom";
import { FlexRowDiv } from './menuStyle';
import { AppContext } from '../../context/appContext';

const FooterComponent = (props) => {
  const { CurrentUser } = useContext(AppContext);

  return (
    <div>
      <div style={{width:"100%", height: '200px', backgroundColor: "#313131", color:'#ffffff', display:"flex", justifyContent:"flex-start"}}>
        <div style={{width: "90%", margin:"auto", textAlign:"center"}}>
          <span>Copyright © {new Date().getFullYear()} StockHub</span>
        </div> 
        {/* <div style={{width: "90%", margin:"auto", textAlign:"center"}}>
          <a href="http://eoddata.com" target="_blank" rel="noopener noreferrer" style={{color: '#888888'}}>EOD Data</a>
        </div>  */}
        <MessageBar interval={10000} />
        <DialogPool />
      </div>
      {CurrentUser && CurrentUser.role === "Admin" && 
        <div style={{width:"100%", height: '600px', backgroundColor: "#3060AF", color:'#FFFFFF', display:"flex", justifyContent:"center"}}>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=updateWithCnn">
            <FlexRowDiv>
              Update with CNN
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=checkProgress">
            <FlexRowDiv>
              Check CNN Progress
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=checkFullProgress">
            <FlexRowDiv>
              Check Yahoo Progress
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=updateSymbol">
            <FlexRowDiv>
              Update Symbol
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=updateBasic">
            <FlexRowDiv>
              Update Basic
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=updateGroup">
            <FlexRowDiv>
              Update Group
            </FlexRowDiv>
          </Link>
          <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=myTest">
            <FlexRowDiv>
              Test
            </FlexRowDiv>
          </Link>
        </div>
      }

    </div>
  );
}

export default FooterComponent;

