import React, { useState } from 'react';
import { withRouter } from "react-router-dom";
import Styled from 'styled-components';
import { ArrowDropDown, ArrowDropUp } from '@material-ui/icons';
import Collapse from '@material-ui/core/Collapse';

const backgroundColor = `#EEEEEE`;
const titleBackgroundColor = `#D2691E`;
const activeBackgroundColor = `#4CAF50`;

const MenuContainer = Styled.div`
cursor: pointer;
color: #FFFFFF;
display: flex;
justify-content: space-between;
align-items: center;
padding: 0px 16px;
height: 50px;
background-color: ${props => props.isMouseOver ? titleBackgroundColor : null};
position: relative;
`;

const DropDownContainer = Styled.div`
border-left: 6px solid ${titleBackgroundColor};
border-right: 6px solid ${titleBackgroundColor};
border-bottom: 6px solid ${titleBackgroundColor};
display: flex;
flex-direction: column;
position: absolute;
top: 50px;
left: 0px;
visibility: ${props => props.isMouseOver ? 'visible' : 'hidden' };
`;

const Menu = Styled.div`
cursor: pointer;
color: #000000;
white-space: nowrap;
padding: 6px 10px;
background-color: ${backgroundColor};
&:hover {
  background-color: ${activeBackgroundColor};
  color: #FFFFFF;
}
`;

const DropDownMenu = (props) => {
  const { collapsible } = props;
  const [titleMouseOver, setTitleMouseOver] = useState(false);
  const [showMenu, setShowMenu ] = useState(false);

  if (collapsible) {
    return (
      <div>
        <MenuContainer isMouseOver={titleMouseOver} onClick={() => setShowMenu((prev) => !prev) }
                      onMouseOver={() => setTitleMouseOver(true)} 
                      onMouseOut={() => setTitleMouseOver(false)}>
            <span>{props.title}</span>
            {!showMenu && <ArrowDropDown fontSize="small" />}
            {showMenu && <ArrowDropUp fontSize="small" />}
        </MenuContainer>
        <Collapse in={showMenu}>
          {props.menus && props.menus.map((item, index) => {
            return (
              <Menu key={index} onClick={() => {props.history.push(item.link);setTitleMouseOver(false)}}>
                {item.name}   
              </Menu>
            )
          })}
        </Collapse>
      </div>
    )
  }

  return (
    <MenuContainer isMouseOver={titleMouseOver} 
                   onMouseOver={() => setTitleMouseOver(true)} 
                   onMouseOut={() => setTitleMouseOver(false)}>
      <span>{props.title}</span>
      <ArrowDropDown fontSize="small" />
      <DropDownContainer isMouseOver={titleMouseOver} >
        {props.menus && props.menus.map((item, index) => {
          return (
            <Menu key={index} onClick={() => {props.history.push(item.link);setTitleMouseOver(false)}}>
              {item.name}   
            </Menu>
          )
        })}
      </DropDownContainer>
    </MenuContainer>
  )
}

export default withRouter(DropDownMenu);