import React, { useEffect, useState, useContext } from "react";
import { Button } from '@material-ui/core';
import { StockService } from '../../service/stock';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import { AppContext } from '../../context/appContext';
import * as signalR from "@microsoft/signalr";

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const BasicProgressComponent = (props) => {
  const { CurrentUser } = useContext(AppContext);
  const { item } = props;
  const [progress, setProgress] = useState(0);
  const [state, setState] = useState('');

  useEffect(() => {
    const hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(`messagehub`)
      .withAutomaticReconnect()
      .build();

    hubConnection.start().then(() => {
      hubConnection.on('basicProgress', (name, message) => {
        if (item.name === name) {
          setProgress(parseInt(message));
        }
      });
      hubConnection.on('basicState', (name, message) => {
        if (item.name === name) {
          setState(message);
        }
      });
    }).catch(err => { });  

    return () => {}
  }, [item.name]);

  return (
      <div style={{borderBottom:'1px solid #eeeeee', paddingBottom: '6px', marginBottom: '6px'}}>
        <div style={{display:'flex', alignItems:'center', alignContent: 'space-between'}}>
          <div style={{flexBasis: '50px', flexGrow: 1}}>{item.name}</div>        
          <div style={{flexBasis: '300px', flexGrow: 1}}>{item.description}</div> 
          <div style={{flexBasis: '200px', flexGrow: 1}}>{item.lastUpdate}</div>
          <div style={{flexBasis: '100px', flexGrow: 1}}>{item.count}</div>
          <div style={{flexBasis: '50px', flexGrow: 1}}>
            <Button variant="contained" color="primary" style={{marginRight:'20px'}}
              onClick={() => StockService.updateBasic(`${item.name}`, CurrentUser.token)}>
              Update
            </Button>    
          </div>
          <div style={{flexBasis: '50px', flexGrow: 1}}>
            <Button variant="contained" color="secondary"
              onClick={() =>StockService.stopUpdateBasic(`${item.name}`, CurrentUser.token)}>
              Cancel
            </Button>
          </div>
          <div style={{flexBasis: '400px', paddingLeft: '10px', flexGrow: 1}}>{state}</div>
        </div>
        <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={progress} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
          </Box>
        </Box>  
      </div>

  );
};
 
export default BasicProgressComponent;