import React, { useState, useEffect, useContext } from "react";
import styled from 'styled-components';
import Pagination from '../common/pagination/index';
import AddIcon from '@material-ui/icons/Add';
import { MyStockService } from '../../service/myStock';
import Tooltip from '../common/tooltip/index';
import { AppContext } from '../../context/appContext';
import StyledButton from '../common/buttons/styledButton';
import StockTable from '../common/stockTable/index';

const Container = styled.div`
padding: 0px 30px 30px 30px;
`;

const TitleBar = styled.div`
padding-left: 20px;
display: flex;
justify-content: space-between;
align-items: center;
@media only screen and (max-width: 1024px) {
  flex-direction: column;
  align-items: flex-start;
} 
`;

const Title = styled.div`
  font-size: 24px;
  font-weight: bold;
  color: #C85050;
`;

const defaultPageSize = 20;

const MyStockComponent = (props) => {  
  const { CurrentUser, dispatch, GroupStockUpdate } = useContext(AppContext);

  const [state, setState] = useState({
    totalPage: 30,
    currentPage: 1,
    pageSize: defaultPageSize,
    totalItems: 0,
    data: null,
    sortMethod: 1,
    exchangeText: '',
    loading: false
  });

  const { totalPage, currentPage, pageSize, data, sortMethod, loading } = state;

  const groupId = props.id; 

  useEffect(() => {
    changePageSize(state.pageSize);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [groupId, GroupStockUpdate]);

  const changePageSize = (size) => {
    if (CurrentUser) {
      setState((prevState) => ({...prevState, loading: true}));
      MyStockService.loadGroupStock(CurrentUser.token, groupId, 0, size, sortMethod).then(res => { 
        let count = Math.floor(res.data.total / size);
        if ((res.data.total % size) !== 0) {
          count++;
        }
        setState((prevState) => ({
          ...prevState,
          loading: false,
          data: res.data.stocks,
          pageSize: size,
          currentPage: 1,
          totalPage: count,
          totalItems: res.data.total,
          exchangeText: res.data.description
        }));
      }, err => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
      });
    }
  }

  const changePage = (num) => {
    setState((prevState) => ({...prevState, loading: true}));
    let start = (num - 1) * pageSize;
    MyStockService.loadGroupStock(CurrentUser.token, groupId, start, pageSize, sortMethod).then(res => {
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: num,
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    })
  }

  const changeSort = (method) => {
    setState((prevState) => ({...prevState, loading: true}));

    MyStockService.loadGroupStock(CurrentUser.token, groupId, 0, pageSize, method).then(res => {
      let count = Math.floor(res.data.total / pageSize);
      if ((res.data.total % pageSize) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        sortMethod: method,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  return (
    <Container>
      <TitleBar>
        <div style={{display: 'flex', alignItems: 'flex-end'}}>
          <Title><b>{props.name}</b></Title>
          <Tooltip placement="right" tooltip="Add new stock">
            <StyledButton 
              style={{ marginLeft:'10px', 
                      backgroundColor: '#3C9F40', 
                      color: '#FFFFFF', 
                      fontSize: '12px',
                      padding: '2px',
                      margin: '0px 0px 2px 20px' }}
              onClick={() => dispatch({type:'SET_NEWSTOCK_GROUP', data:{id:props.id, name:props.name}})}
            >
              <AddIcon style={{color: '#FFFFFF', width: '18px', height: '18px' }}/>
            </StyledButton>  
          </Tooltip>
        </div>
        <div style={{paddingTop:'10px'}}>
          <Pagination totalPages={totalPage}
                      currentPage={currentPage}
                      currentPageSize={pageSize}
                      pageSizes={[10, 20, 30, 50, 100]}
                      onPageChanged = {(num) => changePage(num)}
                      onPageSizeChanged = {(size) => changePageSize(size)}
                      />
        </div>
      </TitleBar>

      <StockTable data={data} sortMethod={sortMethod} loading={loading} changeSort={changeSort} groupId={groupId} />

      { data && data.length > 20 && (
        <div style={{paddingTop:'0px'}}>
            <Pagination totalPages={totalPage}
                        currentPage={currentPage}
                        currentPageSize={pageSize}
                        pageSizes={[10, 20, 30, 50, 100]}
                        onPageChanged = {(num) => changePage(num)}
                        onPageSizeChanged = {(size) => changePageSize(size)}
                        />
        </div>
        )
      }
    </Container>
  )
}

export default MyStockComponent;