import React from 'react';
import Tooltip from '../tooltip/index';
import { HeaderRow, HeaderCell, CenteredCell } from './styled';
import {ArrowDropUp, ArrowDropDown } from '@material-ui/icons';

const SortMethod = {
  Name_ASC: 1,
  Name_DES: 2,
  Asset_ASC: 3,
  Asset_DES: 4,
  Price_ASC: 5,
  Price_DES: 6,
  Change_ASC: 7,
  Change_DES: 8,
  PE_ASC: 9,
  PE_DES: 10,
  Volume_ASC: 11,
  Volume_DES: 12,
  Position_ASC: 13,
  Position_DES: 14,
  Dividend_ASC: 15,
  Dividend_DES: 16,
  Earning_ASC: 17,
  Earning_DES: 18,
  Sector_ASC: 19,
  Sector_DES: 20,
  Industry_ASC: 21,
  Industry_DES: 22,
  Macd_ASC: 23,
  Macd_DES: 24,
};

const TableHeader = (props) => {
  const { changeSort, sortMethod } = props;

  const onChangeSort = (name) => {
    let method = SortMethod.Name_ASC;
    switch(name) {
      case 'Name': 
        if (sortMethod === SortMethod.Name_ASC) method = SortMethod.Name_DES;
        else method = SortMethod.Name_ASC;
        break;
      case 'Asset': 
        if (sortMethod === SortMethod.Asset_ASC) method = SortMethod.Asset_DES;
        else method = SortMethod.Asset_ASC;
        break;
      case 'Price': 
        if (sortMethod === SortMethod.Price_ASC) method = SortMethod.Price_DES;
        else method = SortMethod.Price_ASC;
        break;
      case 'Change': 
        if (sortMethod === SortMethod.Change_ASC) method = SortMethod.Change_DES;
        else method = SortMethod.Change_ASC;
        break;
      case 'PE': 
        if (sortMethod === SortMethod.PE_ASC) method = SortMethod.PE_DES;
        else method = SortMethod.PE_ASC;
        break;
      case 'Volume': 
        if (sortMethod === SortMethod.Volume_ASC) method = SortMethod.Volume_DES;
        else method = SortMethod.Volume_ASC;
        break;
      case 'Position': 
        if (sortMethod === SortMethod.Position_ASC) method = SortMethod.Position_DES;
        else method = SortMethod.Position_ASC;
        break;
      case 'Dividend': 
        if (sortMethod === SortMethod.Dividend_ASC) method = SortMethod.Dividend_DES;
        else method = SortMethod.Dividend_ASC;
        break;
      case 'Earning': 
        if (sortMethod === SortMethod.Earning_ASC) method = SortMethod.Earning_DES;
        else method = SortMethod.Earning_ASC;
        break;
      case 'Sector': 
        if (sortMethod === SortMethod.Sector_ASC) method = SortMethod.Sector_DES;
        else method = SortMethod.Sector_ASC;
        break;
      case 'Industry': 
        if (sortMethod === SortMethod.Industry_ASC) method = SortMethod.Industry_DES;
        else method = SortMethod.Industry_ASC;
        break;
      case 'Macd': 
        if (sortMethod === SortMethod.Macd_ASC) method = SortMethod.Macd_DES;
        else method = SortMethod.Macd_ASC;
        break;

      default:
        break;
    }

    changeSort(method);
  }

  return (
    <HeaderRow>
      <Tooltip tooltip="Stock symbol">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Name')}>
          <CenteredCell>
            <span>Name</span>
            { sortMethod === SortMethod.Name_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Name_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Current price">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Price')}>
          <CenteredCell>
            <span>Price</span>
            { sortMethod === SortMethod.Price_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Price_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Price change percent">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Change')}>
          <CenteredCell>
            <span>Change</span>
            { sortMethod === SortMethod.Change_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Change_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Volume index" tooltip2="Current volume / avg volume">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Volume')}>
          <CenteredCell>
            <span>Volume</span>
            { sortMethod === SortMethod.Volume_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Volume_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="PE ratio">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('PE')}>
          <CenteredCell>
            <span>PE</span>
            { sortMethod === SortMethod.PE_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.PE_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Market capital">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Asset')}>
          <CenteredCell>
            <span>Cap</span>
            { sortMethod === SortMethod.Asset_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Asset_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Price position in 52 weeks" tooltip2="(price - min) / (max - min)">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Position')}>
          <CenteredCell>
            <span>Position</span>
            { sortMethod === SortMethod.Position_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Position_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Dividend rate">
        <HeaderCell style={{width: '9%'}} onClick={() => onChangeSort('Dividend')}>
          <CenteredCell>
            <span>Dividend</span>
            { sortMethod === SortMethod.Dividend_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Dividend_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="MACD">
        <HeaderCell style={{width: '9%'}} noborder onClick={() => onChangeSort('Macd')}>
          <CenteredCell>
            <span>MACD</span>
            { sortMethod === SortMethod.Macd_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Macd_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Sector">
        <HeaderCell style={{width: '9%'}} noborder onClick={() => onChangeSort('Sector')}>
          <CenteredCell>
            <span>Sector</span>
            { sortMethod === SortMethod.Sector_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Sector_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
      <Tooltip tooltip="Industry">
        <HeaderCell style={{width: '9%'}} noborder onClick={() => onChangeSort('Industry')}>
          <CenteredCell>
            <span>Industry</span>
            { sortMethod === SortMethod.Industry_ASC && <ArrowDropUp />} 
            { sortMethod === SortMethod.Industry_DES && <ArrowDropDown />}
          </CenteredCell>
        </HeaderCell>
      </Tooltip>
    </HeaderRow>
  )
}

export default TableHeader;