import Styled from 'styled-components';

const backgroundColor = `#0073A6`;
const activeBackgroundColor = `#D2691E`;
const menuColor = `#FFFFFF`;

const Navbar = Styled.div`
background-color: ${backgroundColor};
display: flex;
flex-direction: row;
justify-content: space-between;
height: 50px;
overflow: visible;
position: sticky;
top: 0;
left: 0;
z-index: 100;
padding-left: 20px;
padding-right: 20px;

@media only screen and (max-width: 700px) {
  display: none;
}
`;


const MobileNavbar = Styled.div`
position: sticky;
top: 0;
left: 0;
z-index: 100;

@media only screen and (min-width: 701px) {
  display: none;
}
`;

const FlexRowDiv = Styled.div`
cursor: pointer;
display: flex;
flex-direction: row;
justify-content: flex-start;
align-items: center;
height: 50px;
padding-left: 16px;
padding-right: 16px;
color: ${menuColor};
&:hover {
  background-color: ${activeBackgroundColor};
}
`;

const DropdownMenu = Styled.div`
padding: 10px 20px;
color: ${menuColor};
&:hover {
  background-color: ${activeBackgroundColor};
}
`;

const Dropdown = Styled.div`
&:hover {
  background-color: ${activeBackgroundColor};
}
&:hover > div {
  visibility: visible;
}
`;

const DropdownContent = Styled.div`
  visibility: hidden;
  position: absolute;
  background-color: white;
  border-left: 6px solid ${activeBackgroundColor};
  border-right: 6px solid ${activeBackgroundColor};
  border-bottom: 6px solid ${activeBackgroundColor};
`;

const MenuContainer = Styled.div`
margin-top: 2px;
background-color: ${backgroundColor};
padding: 6px;
`;

export { Navbar, MobileNavbar, FlexRowDiv, DropdownMenu, Dropdown, DropdownContent, MenuContainer }