import React from 'react';
import Slider from '@material-ui/core/Slider';
import Input from '@material-ui/core/Input';

function valuetext(value) {
  return `${value}°C`;
}

const RangeSlider = (props) => {
  const { min, max, step, start, end, label, onChange } = props;

  const handleChange = (event, newValue) => {
    onChange(newValue);
  };

  const onMinChanged = (event) => {
    if (event.target.value !== '') {
      onChange([Number(event.target.value), end]);
    }
  };

  const onMinBlur = () => {
    // if (value < 0.1) {
    //   setValue(0.1);
    // } else if (value > 1000) {
    //   setValue(1000);
    // }
  };

  const onMaxChanged = (event) => {
    if (event.target.value !== '') {
      onChange([start, Number(event.target.value)]);
    }
  };

  const onMaxBlur = () => {
    // if (value < 0.1) {
    //   setValue(0.1);
    // } else if (value > 1000) {
    //   setValue(1000);
    // }
  };

  return (
    <div>
      <div style={{display: 'flex', justifyContent: 'space-between', width: '100%', alignItems: 'center'}}>
        <Input value={start} margin="dense"
          onChange={onMinChanged}
          onBlur={onMinBlur}
          inputProps={{ step, min, max, type: 'number', 'aria-labelledby': 'input-slider' }}
        />
        <span>{label}</span>
        <Input value={end} margin="dense"
          onChange={onMaxChanged}
          onBlur={onMaxBlur}
          inputProps={{ step, min, max, type: 'number', 'aria-labelledby': 'input-slider' }}
        />              
      </div>

      <Slider
        value={[start, end]}
        min={min}
        max={max}
        step={step}
        onChange={handleChange}
        valueLabelDisplay="auto"
        aria-labelledby="range-slider"
        getAriaValueText={valuetext}
      />
    </div> 
  );
}

export default RangeSlider;
