import React, { useEffect, useState, useContext } from "react";
import { Paper } from '@material-ui/core';
import { StockService } from '../../service/stock';
import Styled from 'styled-components';
import { AppContext } from '../../context/appContext';
import UpdateWithCnnProgress from './updateDailyInfoProgress';

const TitleBar = Styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px;
  background-Color: #4CAF50;
  color: #FFFFFF;
  border-radius: 6px 6px 0px 0px;
`;

const UpdateWithCnnComponent = () => {
  const { CurrentUser } = useContext(AppContext);
  const [ sectionLength, setSectionLength ] = useState(0);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    if (CurrentUser && CurrentUser.token) {
      StockService.getSetting(10, CurrentUser.token).then(res => {
        setSectionLength(res.data.sectionLength);
      });
    }
  }, [CurrentUser]);

  return (
    <Paper elevation={4} style={{borderRadius:'6px', width: '80%', margin:'20px auto'}}>
        <TitleBar>
          <span style={{fontSize:'20px', fontWeight:'bold'}}>Update CNN Business Data</span>
        </TitleBar>

        { sectionLength > 0 &&
          <div style={{padding:'10px'}}>
            <UpdateWithCnnProgress start={0} end={sectionLength - 1} />
            <UpdateWithCnnProgress start={sectionLength} end={sectionLength * 2 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 2} end={sectionLength * 3 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 3} end={sectionLength * 4 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 4} end={sectionLength * 5 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 5} end={sectionLength * 6 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 6} end={sectionLength * 7 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 7} end={sectionLength * 8 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 8} end={sectionLength * 9 - 1} />
            <UpdateWithCnnProgress start={sectionLength * 9} end={sectionLength * 10 - 1} />   
          </div>
        }
    </Paper>
  );
};
 
export default UpdateWithCnnComponent;