import React, { useEffect, useState, useContext } from "react";
import Styled from 'styled-components';
import { withStyles } from '@material-ui/core/styles';
import { Button, Box, LinearProgress, Typography, Paper } from '@material-ui/core';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';

const TitleBar = Styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px;
  background-Color: #4CAF50;
  color: #FFFFFF;
  border-radius: 6px 6px 0px 0px;
`;

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const CheckFullProgressComponent = () => {
  const { CurrentUser } = useContext(AppContext);
  const [ yahooProgress, setYahooProgress] = useState(0);
  const [ yahooMessage, setYahooMessage] = useState('');
  const [ sectorProgress, setSectorProgress] = useState(0);
  const [ sectorMessage, setSectorMessage] = useState('');
  const [ macdProgress, setMACDProgress] = useState(0);
  const [ macdMessage, setMACDMessage] = useState('');

  // useEffect(() => {
  //   window.scrollTo(0, 0);

  //   const hubConnection = new signalR.HubConnectionBuilder()
  //     .withUrl(`messagehub`)
  //     .withAutomaticReconnect()
  //     .build();

  //   hubConnection.start().then(() => {
  //     hubConnection.on('yahooProgress', (message) => {
  //       setYahooProgress(parseInt(message));
  //     });
  //     hubConnection.on('yahooMessage', (message) => {
  //       setYahooMessage(message);
  //     });
  //   }).catch(err => { });  

  //   return () => {}
  // }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    let interval = null;
    if (CurrentUser) {
       interval = setInterval(() => {
        StockService.getProgress('yahoo', CurrentUser.token).then(res => {
          setYahooProgress(res.data.progress);
          setYahooMessage(res.data.message);
        })
        StockService.getProgress('sector', CurrentUser.token).then(res => {
          setSectorProgress(res.data.progress);
          setSectorMessage(res.data.message);
        })
        StockService.getProgress('macd', CurrentUser.token).then(res => {
          setMACDProgress(res.data.progress);
          setMACDMessage(res.data.message);
        })
      }, 5000);
    }
    return () => clearInterval(interval);
  }, [CurrentUser]);

  return (
    <Paper elevation={4} style={{borderRadius:'6px', width: '80%', margin:'20px auto'}}>
        <TitleBar>
          <span style={{fontSize:'20px', fontWeight:'bold'}}>Update with Yahoo</span>
          {/* <div style={{marginLeft: '20px'}}>
            <Checkbox disabled checked={enabled} color="primary" inputProps={{ 'aria-label': 'secondary checkbox' }}  />
          </div>
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="primary" style={{marginRight:'20px'}}
              onClick={() => StockService.enableYahoo(CurrentUser.token).then(res => {
                setEnabled(true);
              }) }>
              Enable
            </Button>    
          </div>
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="secondary"
              onClick={() => StockService.disableYahoo(CurrentUser.token).then(res => {
                setEnabled(false);
              }) }>
              Disable
            </Button>
          </div> */}
        </TitleBar>

        <div style={{padding:'10px'}}>
          {/* <div style={{marginTop:'10px', paddingBottom: '10px', borderBottom: '1px solid #CCCCCC'}}>
            <div style={{display:'flex', alignItems:'center'}}>
              <span>Auto Update {autoState}</span>
            </div>
            <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
              <Box width="100%" mr={1}>
                <BorderLinearProgress variant="determinate" value={autoProgress} />
              </Box>
              <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${autoProgress}%`}</Typography>
              </Box>
            </Box>  
          </div> */}

          <div style={{marginTop:'10px'}}>
            <div style={{display:'flex', alignItems:'center', justifyContent: 'flex-start'}}>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="primary" style={{marginRight:'20px'}}
                  onClick={() => StockService.updateYahoo(CurrentUser.token)}>
                  Update PE
                </Button>    
              </div>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="secondary"
                  onClick={() =>StockService.stopUpdateYahoo(CurrentUser.token)}>
                  Cancel
                </Button>
              </div>
              <div style={{marginLeft: '20px'}}>
                {yahooMessage}
              </div>
            </div>
            <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
              <Box width="100%" mr={1}>
                <BorderLinearProgress variant="determinate" value={yahooProgress} />
              </Box>
              <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${yahooProgress}%`}</Typography>
              </Box>
            </Box>  
          </div>

        </div>

        <div style={{padding:'10px'}}>

          <div style={{marginTop:'10px'}}>
            <div style={{display:'flex', alignItems:'center', justifyContent: 'flex-start'}}>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="primary" style={{marginRight:'20px'}}
                  onClick={() => StockService.updateYahooSector(CurrentUser.token)}>
                  Update Sector
                </Button>    
              </div>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="secondary"
                  onClick={() =>StockService.stopUpdateYahooSector(CurrentUser.token)}>
                  Cancel
                </Button>
              </div>
              <div style={{marginLeft: '20px'}}>
                {sectorMessage}
              </div>
            </div>
            <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
              <Box width="100%" mr={1}>
                <BorderLinearProgress variant="determinate" value={sectorProgress} />
              </Box>
              <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${sectorProgress}%`}</Typography>
              </Box>
            </Box>  
          </div>

        </div>

        
        <div style={{padding:'10px'}}>

          <div style={{marginTop:'10px'}}>
            <div style={{display:'flex', alignItems:'center', justifyContent: 'flex-start'}}>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="primary" style={{marginRight:'20px'}}
                  onClick={() => StockService.updateYahooMACD(CurrentUser.token)}>
                  Update MACD
                </Button>    
              </div>
              <div style={{marginLeft: '20px'}}>
                <Button variant="contained" color="secondary"
                  onClick={() =>StockService.stopUpdateYahooMACD(CurrentUser.token)}>
                  Cancel
                </Button>
              </div>
              <div style={{marginLeft: '20px'}}>
                {macdMessage}
              </div>
            </div>
            <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
              <Box width="100%" mr={1}>
                <BorderLinearProgress variant="determinate" value={macdProgress} />
              </Box>
              <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${macdProgress}%`}</Typography>
              </Box>
            </Box>  
          </div>

        </div>

    </Paper>
  );
};
 
export default CheckFullProgressComponent;