import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import StyledButton from '../common/buttons/styledButton';
import styled from 'styled-components';
import Tooltip from '../common/tooltip/index';

const DataRow = styled.div`
font-size: 16px;
position: relative;
overflow: hidden;
padding: 3px 0px 3px 6px;
display: flex;
justify-content: space-between;
align-items: center;
border-radius: ${props => props.lastRow ? '0px 0px 6px 6px' : '' };
background-color: ${props => props.disabled ? '#CCCCCC' : '' };
&:hover {
  background-color: ${props => props.disabled ? '#CCCCCC' : '#dddddd' };
}
`;

const DataCell = styled.div`
cursor: default;
white-space: nowrap;
display: flex;
justify-content: flex-start;
align-items: center;
padding: 0px;
flex-grow: 1;
flex-shrink: 0;
text-align: left;
flex-basis: 60px;
`;

const CellText = styled.span`
cursor: default;
padding-bottom: 3px;
`;

const ActionContainer = styled.div`
position: absolute;
right: 0;
top: 0;
display: flex;
flex-direction: row;
align-items: center;
height: 30px;
`;

const GroupRow = (props) => {
  const {item, index } = props;
  const [mouseOver, setMouseOver] = useState(false);

  const onAction = (action) => {
    if (action === "row") {
      props.onSelected(item);
    } else if (action === "edit") {
      props.onEdit(item);
    } else if (action === "delete") {
      props.onDelete(item);
    }
  }

  const renderRow = (item) => {
    if (item.description && item.description.trim() !== '') {
      return (
        <Tooltip placement="right" tooltip={item.description}>
          <DataCell>
              <CellText>{item.name}</CellText>
          </DataCell>
      </Tooltip>
      )
    }

    return (
      <DataCell>
          <CellText>{item.name}</CellText>
      </DataCell>
    )
  }

  return (
    <DataRow key={index} first={index === 0} lastRow={props.lastRow} 
        onClick={(e) => {e.stopPropagation(); onAction("row"); }}
        onMouseEnter={() => setMouseOver(true)}
        onMouseLeave={() => setMouseOver(false)}
    >
      {renderRow(item)}
      {mouseOver && item.name !== "My Favorite" && (
        <ActionContainer>
          <Tooltip placement="bottom" tooltip="Edit this group">
            <StyledButton
              onClick={(e) => {e.stopPropagation(); onAction("edit"); }}
              style={{ marginRight:'10px', 
                  backgroundColor: '#1976D2', 
                  color: '#FFFFFF', 
                  padding: '6px 6px 6px 6px',
            }}
            >
              <EditIcon style={{width: '12px', height: '12px' }} />
            </StyledButton>
          </Tooltip>
          <Tooltip placement="bottom" tooltip="Delete this group">
            <StyledButton
              onClick={(e) => {e.stopPropagation(); onAction("delete"); }}
              style={{ marginRight:'10px', 
                  backgroundColor: '#DA0050', 
                  color: '#FFFFFF', 
                  padding: '6px 6px 6px 6px',
            }}
            >
              <DeleteIcon style={{width: '12px', height: '12px' }} />
            </StyledButton>
          </Tooltip>
        </ActionContainer>
      )}
    </DataRow>
  )
}

export default withRouter(GroupRow);