import React, { useContext, useRef, useState } from "react";
import { Link, withRouter } from "react-router-dom";
import { ArrowDropDown, AccountCircle } from '@material-ui/icons';
import { AppContext } from '../../context/appContext';
import { FlexRowDiv, Dropdown, DropdownContent } from './menuStyle';
import Styled from 'styled-components';

const backgroundColor = `#EEEEEE`;
const activeBackgroundColor = `#4CAF50`;

const Menu = Styled.div`
cursor: pointer;
color: #000000;
white-space: nowrap;
padding: 6px 10px;
background-color: ${backgroundColor};
&:hover {
  background-color: ${activeBackgroundColor};
  color: #FFFFFF;
}
`;

const UserMenuComponent = (props) => {
  const { CurrentUser, dispatch } = useContext(AppContext);
  const [ right, setRight ] = useState(0);
  const myRef = useRef(null);

  const updateDropdown = () => {
    if (myRef) {
      setRight(Math.floor(window.innerWidth - myRef.current.getBoundingClientRect().right) - 16);
    }
  }

  return (
    <div style={{ display: 'flex', flexDirection: 'row' }}>
      {CurrentUser ?
      <Dropdown>
        <FlexRowDiv ref={myRef} onMouseOver={() => updateDropdown() }>
          <AccountCircle fontSize="default" />
          <ArrowDropDown fontSize="default" />
        </FlexRowDiv>
        <DropdownContent style={{right: right}}>
          <Menu onClick={() => props.history.push({ pathname: '/', search: '?c=changepassword' })}>
            <span>Change password</span>
          </Menu>
          <Menu onClick={() => {
                dispatch({type:'LOGOUT'});
                localStorage.removeItem('loginUserInfo');
                localStorage.removeItem('loginTime');
                props.history.push('/');
                }}>
                <span>Logout</span>
          </Menu>
        </DropdownContent>
      </Dropdown>
      :
      <Link style={{ textDecoration: 'none', marginRight: '10px' }} to="/?c=login">
        <FlexRowDiv>
          Sign In / Register
        </FlexRowDiv>
      </Link>
      }
    </div>          
  );
}

export default withRouter(UserMenuComponent);

