import React from "react";
import AddNewStockToGroupDialog from '../common/dialog/addNewStockToGroupDialog';
import AddStockToGroupDialog from '../common/dialog/addStockToGroupDialog';
import DeleteStockDialog from '../common/dialog/deleteStockDialog';
import StockWebSiteDialog from '../common/dialog/stockWebSiteDialog';

const DialogPool = (props) => {
  return (
    <div>
      <AddNewStockToGroupDialog />
      <AddStockToGroupDialog title={"Add Stock To Group"} />
      <StockWebSiteDialog />
      <DeleteStockDialog />
    </div>
  );
}

export default DialogPool;