import React, { useContext, useState } from 'react';
import Tooltip from '../tooltip/index';
import { Forward } from '@material-ui/icons';
import StarIcon from '@material-ui/icons/Star';
import BottomIcon from '@material-ui/icons/VerticalAlignBottom';
import MonetizationIcon from '@material-ui/icons/MonetizationOn';
import ImportantIcon from '@material-ui/icons/LabelImportant';
import { DataRow, DataCell, CenteredCell, SpaceBetween } from './styled';
import SymbolCell from './symbolCell';
import SymbolDeleteCell from './symbolDeleteCell';
import ClickHandler from '../clickHandler';
import { AppContext } from '../../../context/appContext';
import { GetLinkUrl } from '../dialog/webSiteLinks/index';

const TableRow = (props) => {
  const { dispatch, StockLinkType, ActiveStock } = useContext(AppContext);
  const [mouseOver, setMouseOver] = useState(false);
  const { item, groupId} = props;

  const onRowClick = (name) => {
      dispatch({ type: 'SET_SELECTED_SYMBOL', data: name });
      dispatch({ type: 'SET_ACTIVE_SYMBOL', data: name });
  }

  const onRowDbClick = (name) => {
    dispatch({ type: 'SET_ACTIVE_SYMBOL', data: name });
    window.open(GetLinkUrl(StockLinkType, name), "_Blank")
  }

  return (
      <DataRow selected={item.name === ActiveStock} onClick={ClickHandler(onRowClick, onRowDbClick, item.name)} onMouseEnter={() => setMouseOver(true)} onMouseLeave={() => setMouseOver(false)}>
      <DataCell style={{width: '9%'}}>
        {!groupId && <SymbolCell item={item} mouseOver={mouseOver} />}
        {groupId && <SymbolDeleteCell item={item} groupId={groupId} mouseOver={mouseOver} />}
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <span>{item.price}</span>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <SpaceBetween>
            <div>
              {item.change}%
            </div> 
            <div style={{width: '20px', height: '20px'}}>
              {item.change > 0 && <Forward style={{color:'green', transform:'rotate(270deg)', width:'20px', height: '20px'}} />}
              {item.change < 0 && <Forward style={{color:'red', transform:'rotate(90deg)', width:'20px', height: '20px'}} />}
            </div>
          </SpaceBetween>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <SpaceBetween>
            <div>
              {item.vIndex.toFixed(2)}
            </div> 
            <div style={{width: '20px', height: '20px'}}>
              {item.vIndex >= 1.5 && 
                <Tooltip tooltip="High volume" tooltip2="1.5+ of avg volume">
                  <ImportantIcon style={{color:'red', width:'20px', height: '20px'}} />
                </Tooltip>
              }
            </div>
          </SpaceBetween>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <SpaceBetween>
            <div>
              {item.pe}
            </div> 
            <div style={{width: '20px', height: '20px'}}>
              {item.pe >= 10 && item.pe <= 20 && 
              <Tooltip tooltip="Valued stock" tooltip2="PE >= 10 and <= 20">
                <StarIcon style={{color:'#FFB400', width:'20px', height: '20px'}} />
              </Tooltip>
              }
            </div>
          </SpaceBetween>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          { item.asset && !item.asset.includes("N/A") && item.asset}
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <SpaceBetween>
            <div>
              {item.y1.toFixed(2)}
            </div> 
            <div style={{width: '20px', height: '20px'}}>
              {item.price > 0 && item.y1 <= 0.1 && 
              <Tooltip tooltip="Approach 52 weeks low" tooltip2="Position <= 0.1">
                <BottomIcon style={{color:'red', width:'20px', height: '20px'}} />
              </Tooltip>
              }
              {item.price > 0 && item.y1 >= 0.9 && 
              <Tooltip tooltip="Approach 52 weeks high" tooltip2="Position >= 0.9">
                <BottomIcon style={{color:'green', transform:'rotate(180deg)', width:'20px', height: '20px'}} />
              </Tooltip>
              }
            </div>
          </SpaceBetween>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          <SpaceBetween>
            <div>
              {item.div > 0 && item.div.toFixed(2)}
            </div> 
            <div style={{width: '20px', height: '20px'}}>
              {item.div > 2 && 
              <Tooltip tooltip="Hight dividend rate" tooltip2="2%+ dividend rate">
                <MonetizationIcon style={{color:'red', width: '20px', height: '20px'}} />
              </Tooltip>
              }
            </div>
          </SpaceBetween>
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          {item.macd && item.macd.toFixed(2)}
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          {item.sector}
        </CenteredCell>
      </DataCell>
      <DataCell style={{width: '9%'}}>
        <CenteredCell>
          {item.industry}
        </CenteredCell>
      </DataCell>
    </DataRow>
  )
}

export default TableRow;