import React, { useContext, useState} from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import StyledButton from '../buttons/styledButton';
import StyledCloseButton from '../buttons/styledCloseButton';
import Zoom from '@material-ui/core/Zoom';
import { MyStockService } from '../../../service/myStock';
import { AppContext } from '../../../context/appContext';
import TextInput from '../../common/textInput';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const AddNewStockToGroupDialog = (props) => {
  const { CurrentUser, dispatch, GroupForNewStock } = useContext(AppContext);
  const [ symbol, setSymbol ] = useState('');
  const [ symbolError, setSymbolError ] = useState(null);

  const onAddStock = () => {
    const name = symbol.trim();
    if (name.length === 0) {
      setSymbolError("Name is required");
      return;
    }

    MyStockService.addToGroupByName(CurrentUser.token, GroupForNewStock.id, name).then(res => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `${name} is added to group: ${GroupForNewStock.name}`}});
      dispatch({type:'SET_NEWSTOCK_GROUP', data:null});
      dispatch({type:'SYMBOL_REMOVED'});   
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to add ${name} to group: ${GroupForNewStock.name}`}});
    })
  }

  const onClose = () => {
    dispatch({type:'SET_NEWSTOCK_GROUP', data:null})
  }

  const onSymbolChanged = (value) => {
    setSymbolError(null);
    setSymbol(value);
  }
  
  return (
      <Dialog
        TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="sm"
        open={GroupForNewStock !== null}
        onClose={() => onClose()}
        aria-labelledby="max-width-dialog-title"
      >
        <DialogTitle id="max-width-dialog-title" 
          style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor:'#F5F5F5',
           }}
        >
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
            <div style={{fontSize: '18px', fontWeight:'bold'}}>
              Add Stock
            </div>
            <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => onClose()} />
          </div>
        </DialogTitle>
        <DialogContent style={{padding: '0px', margin: '0px'}}>

            <div style={{padding: '10px 20px 20px 20px'}}>
              <TextInput name="newSymbol" label="Name" type="text" value={symbol} errorMessage={symbolError} onChange={onSymbolChanged} />
            </div>

            <div style={{position:'sticky', bottom: '0',
                  padding: '10px 30px 6px 0px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-end',
                  backgroundColor: '#FCFCFC',
                  borderTop: '1px solid #EEEEEE',
                }}
              > 
              <StyledButton 
                type="button"
                style={{ marginRight:'10px', 
                        backgroundColor: '#3C9F40', 
                        color: '#FFFFFF', 
                        fontSize: '14px',
                        padding: '8px 24px 8px 24px',
                        marginBottom: '2px',
                  }}
                onClick={onAddStock}
              >
                Add
              </StyledButton>
              <StyledButton 
                type="button"
                style={{ marginLeft:'10px', 
                        backgroundColor: '#E0E0E0', 
                        color: '#000000', 
                        fontSize: '14px',
                        padding: '8px 16px 8px 16px',
                        marginBottom: '2px',
                  }}
                  onClick={() => onClose()}
              >
                Cancel
              </StyledButton>
            </div>
        </DialogContent>
      </Dialog>
  );
}

export default AddNewStockToGroupDialog;
