import React, { useEffect, useState, useContext } from "react";
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import { Button } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const DailySectionProgressComponent = (props) => {
  const { CurrentUser } = useContext(AppContext);
  const { start, end } = props;
  const [progress, setProgress] = useState(0);
  const [state, setState] = useState('');
  const name = `${start}-${end}`;

  useEffect(() => {
    const interval = setInterval(() => {
      StockService.getProgress(`${name}`, CurrentUser.token).then(res => {
        setProgress(res.data.progress);
        setState(res.data.message);
       })
    }, 5000);
    return () => clearInterval(interval);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [name]);

  return (
      <div style={{marginTop:'10px'}}>
        <div style={{display:'flex', alignItems:'center', justifyContent: 'flex-start'}}>
          <div style={{width: '100px'}}>{name}</div>        
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="primary" style={{marginRight:'20px'}}
              onClick={() => StockService.updateCnnDaily(`${name}`, start, end, CurrentUser.token)}>
              Update
            </Button>    
          </div>
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="secondary"
              onClick={() =>StockService.stopUpdateCnnDaily(`${name}`, CurrentUser.token)}>
              Cancel
            </Button>
          </div>
          <div style={{marginLeft: '20px'}}>
            {state}
          </div>
        </div>
        <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={progress} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
          </Box>
        </Box>  
      </div>
  );
};
 
export default DailySectionProgressComponent