import React, { useEffect, useState, useContext } from "react";
import { Paper } from '@material-ui/core';
import { StockService } from '../../service/stock';
import Styled from 'styled-components';
import { AppContext } from '../../context/appContext';
import SectionProgress from './checkSectionProgress';
import { Button } from '@material-ui/core';
import Checkbox from '@material-ui/core/Checkbox';

const TitleBar = Styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px;
  background-Color: #4CAF50;
  color: #FFFFFF;
  border-radius: 6px 6px 0px 0px;
`;

const CheckProgressComponent = () => {
  const { CurrentUser } = useContext(AppContext);
  const [ enabled, setEnabled ] = useState(true);
  const [ sectionLength, setSectionLength ] = useState(0);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  
  useEffect(() => {
    if (CurrentUser && CurrentUser.token) {
      StockService.getSetting(10, CurrentUser.token).then(res => {
        setSectionLength(res.data.sectionLength);
      })  
    }
  }, [CurrentUser]);

  return (
    <Paper elevation={4} style={{borderRadius:'6px', width: '80%', margin:'20px auto'}}>
        <TitleBar>
          <span style={{fontSize:'20px', fontWeight:'bold'}}>Check Daily Update</span>
          <div style={{marginLeft: '20px'}}>
            <Checkbox disabled checked={enabled} color="primary" inputProps={{ 'aria-label': 'secondary checkbox' }}  />
          </div>
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="primary" style={{marginRight:'20px'}}
              onClick={() => StockService.enableCnn(CurrentUser.token).then(res => {
                setEnabled(true);
              }) }>
              Enable
            </Button>    
          </div>
          <div style={{marginLeft: '20px'}}>
            <Button variant="contained" color="secondary"
              onClick={() => StockService.disableCnn(CurrentUser.token).then(res => {
                setEnabled(false);
              }) }>
              Disable
            </Button>
          </div>
        </TitleBar>

          <div style={{padding:'10px'}}>
            { sectionLength > 0 &&
              <div>
                <SectionProgress start={0} end={sectionLength - 1} />
                <SectionProgress start={sectionLength} end={sectionLength * 2 - 1} />
                <SectionProgress start={sectionLength * 2} end={sectionLength * 3 - 1} />
                <SectionProgress start={sectionLength * 3} end={sectionLength * 4 - 1} />
                <SectionProgress start={sectionLength * 4} end={sectionLength * 5 - 1} />
                <SectionProgress start={sectionLength * 5} end={sectionLength * 6 - 1} />
                <SectionProgress start={sectionLength * 6} end={sectionLength * 7 - 1} />
                <SectionProgress start={sectionLength * 7} end={sectionLength * 8 - 1} />
                <SectionProgress start={sectionLength * 8} end={sectionLength * 9 - 1} />
                <SectionProgress start={sectionLength * 9} end={sectionLength * 10 - 1} />       
              </div>
            }
          </div>
    </Paper>
  );
};
 
export default CheckProgressComponent;