import React from "react";
import ContentTemplate from './component/contentTemplate/index';

class DefaultComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {id: null, token: null}
  }

  getParas() {
    let dict = {};
    let params = this.props.location.search.slice(1, this.props.location.search.length).split('&');
    params.forEach(element => {
      let items = element.split('=');
      if (items.length === 2) {
        dict[items[0]] = items[1];
      }
    });
    return dict;
  }

  componentDidMount() {
    if (this.props.location.search) {
      var dict = {};
      var params = this.props.location.search.slice(1, this.props.location.search.length).split('&');
      params.forEach(element => {
        let items = element.split('=');
        if (items.length === 2) {
          dict[items[0]] = items[1];
        }
      });

      if (dict['c']) { this.setState({c: dict['c']})}
      if (dict['p']) { this.setState({p: dict['p']})}
    }
  }

  render() {
    const paras = this.getParas();
    const c = paras['c'] ?? '';

    return (
      <div id="RootDiv" style={{margin: '0px'}}>
        <ContentTemplate name={c} paras={paras} />
      </div>    
    )
  }
}

export default DefaultComponent;
