import React, { useContext } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Zoom from '@material-ui/core/Zoom';
import StyledCloseButton from '../buttons/styledCloseButton';
import StyledButton from '../buttons/styledButton';
import { AppContext } from '../../../context/appContext';
import { MyStockService } from '../../../service/myStock';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const DeleteStockDialog = () => {
  const { CurrentUser, dispatch, StockToDelete } = useContext(AppContext);

  const onClose = () => {
    dispatch({type:'SET_DELETE_SYMBOL', data: null});
  }

  const onDelete = () => {
    if (!StockToDelete) return;

    MyStockService.deleteFromGroup(CurrentUser.token, StockToDelete.groupId, StockToDelete.id).then(res => {
      dispatch({type:'SYMBOL_REMOVED', data: null});      
      onClose();
      dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `Symbol ${StockToDelete.name} is removed`}});
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to remove symbol ${StockToDelete.name}`}});
    })
  }

  return (
    <Dialog
      open={StockToDelete !== null}
      onClose={onClose}
      TransitionComponent={Transition}
      keepMounted
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="max-width-dialog-title" 
        style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor: '#F5F5F5' }}
      >
        <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
          <div style={{fontSize: '18px', fontWeight:'bold'}}>{StockToDelete && StockToDelete.name }</div>
          <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={onClose} />
        </div>
      </DialogTitle>
      <DialogContent style={{padding: '0px', margin: '0px'}}>
        <div style={{padding: '20px 60px 20px 20px'}}>
          <div style={{fontSize:'20px', margin:'4px 0px 20px 0px'}}>Are you sure to remove this symbol?</div>
        </div>

        <div style={{position:'sticky', bottom: '0',
                          padding: '10px 30px 6px 0px',
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'flex-end',
                          backgroundColor: '#FCFCFC',
                          borderTop: '1px solid #EEEEEE',
                        }}
                      > 
          <StyledButton 
            onClick={onDelete}
            style={{ marginRight:'10px', 
                    backgroundColor: '#DA0050', 
                    color: '#FFFFFF', 
                    fontSize: '14px',
                    padding: '8px 24px 8px 24px',
                    marginBottom: '2px',
              }}
          >
            Delete
          </StyledButton>
          <StyledButton 
            onClick={onClose} 
            style={{ marginLeft:'10px', 
                    backgroundColor: '#E0E0E0', 
                    color: '#000000', 
                    fontSize: '14px',
                    padding: '8px 16px 8px 16px',
                    marginBottom: '2px',
              }}
          >
            Cancel
          </StyledButton>
        </div>
      </DialogContent>
    </Dialog>
  );
}

export default DeleteStockDialog;