import axios from 'axios';
import ServerUrl from "./server";

export const MyGroupService = {
    loadGroups,
    addGroup,
    updateGroup,
    deleteGroup,    
};

function loadGroups(token, sortASC) {
  let sortName = "asc";
  if (!sortASC) {
    sortName = "desc";
  }
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyGroups/${sortName}`,
    headers: {'Authorization': "bearer " + token}
  });
}

function addGroup(Name, Description, token) {
  return axios({
    method: 'post',
    url: `${ServerUrl}api/MyGroups`,
    data: {Name, Description},
    headers: {'Authorization': "bearer " + token}
  });
};

function updateGroup(id, name, description, token) {
  return axios({
    method: 'post',
    url: `${ServerUrl}api/MyGroups/updateGroup/${id}`,
    data: {name, description},
    headers: {'Authorization': "bearer " + token, 'Content-Type': 'application/json;charset=UTF-8',}
  });
};

function deleteGroup(id, token) {
  return axios({
    method: 'get',
    url: `${ServerUrl}api/MyGroups/deleteGroup/${id}`,
    headers: {'Authorization': "bearer " + token}
  });
};


