
import axios from 'axios';
import ServerUrl from "./server";

export const AuthService = {
    login,
    register,
    confirmEmail,
    changePassword,
    sendResetCode,
    resetPassword,
    getUserInfo,
    loadUsers,
    setUserInfo
};

function login(username, password) {
  return axios.post(`${ServerUrl}users/authenticate`, {username, password}, {
      'Content-Type': 'application/json;charset=UTF-8',
  });
}

function loadUsers(token) {
  return axios({
    method: 'get',
    url: `${ServerUrl}Users`,
    headers: {'Authorization': 'bearer ' + token}
});
}

function register(email, password) {
    return axios.post(`${ServerUrl}Users`, {username: email, password, resetcode: window.location.href});
}

function confirmEmail(email, code) {
  return axios.post(`${ServerUrl}Users/confirmEmail`, {username: email, password: code}, {
    'Content-Type': 'application/json;charset=UTF-8'});
}

function resetPassword(email, resetcode, password) {
  return axios.post(`${ServerUrl}Users/resetpassword`, {username: email, resetcode, password});
}

function changePassword(password, newpassword, token) {
  return axios({
      method: 'post',
      url: `${ServerUrl}Users/changePassword`,
      data: {username: password, password: newpassword},
      headers: {'authorization': 'bearer ' + token}
  });
}

function sendResetCode(username) {
  return axios.post(`${ServerUrl}Users/forgetPassword`, {username, password: window.location.href}, {
    'Content-Type': 'application/json;charset=UTF-8',
  })
}

function getUserInfo() {
  let loginTime = localStorage.getItem('loginTime');
  let userInfo = null;
  if (loginTime) {
      let currentTime = new Date().getTime();
      if ((currentTime - loginTime) < 8 * 3600000) {  // 8 hours
          let loginUserInfo = localStorage.getItem('loginUserInfo');
          if (loginUserInfo) {
              userInfo = JSON.parse(loginUserInfo);
          }
      }
      else {
        localStorage.removeItem("loginUserInfo");
        localStorage.removeItem("loginTime");
      }
  }
  return userInfo;
}

function setUserInfo(userInfo) {
  localStorage.setItem("loginUserInfo", userInfo);
  localStorage.setItem("loginTime", new Date().getTime());
}
