import React, { useEffect, useContext, useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Zoom from '@material-ui/core/Zoom';
import StyledCloseButton from '../buttons/styledCloseButton';
import StyledButton from '../buttons/styledButton';
import { MyGroupService } from '../../../service/myGroup';
import { MyStockService } from '../../../service/myStock';
import { AppContext } from '../../../context/appContext';
import styled from 'styled-components';
import CheckIcon from '@material-ui/icons/Check';
import Tooltip from '../tooltip/index';

const GroupRow = styled.div`
cursor: default;
position: relative;
overflow: hidden;
padding: 2px 0px 2px 4px;
display: flex;
justify-content: flex-start;
align-items: center;
font-size: 16px;

&:hover {
  background-color: #dddddd;
}
`;

const Header = styled.div`
font-size: 20px;
margin-bottom: 4px;
`;

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const AddStockToGroupDialog = (props) => {
  const { title } = props;
  const { CurrentUser, dispatch, StockToAdd } = useContext(AppContext);
  const [groups, setGroups] = useState([]);
  const [selectedGroup, setSelectedGroup] = useState(null);

  useEffect(() => {
    if (CurrentUser && StockToAdd) {
      MyGroupService.loadGroups(CurrentUser.token, true).then(res => {
        setGroups(res.data);
      });      
    }
  }, [StockToAdd, CurrentUser])

  const Add = () => {
    if (CurrentUser) {
      MyStockService.addToGroup(CurrentUser.token, selectedGroup.id, StockToAdd.id).then(resp => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: `${StockToAdd.name} is added to group: ${selectedGroup.name}`}});
        close();
      }).catch(() => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to add ${StockToAdd.name} to group: ${selectedGroup.name}`}});
      });
    }
  }

  const close = () => {
    dispatch({type:'SYMBOL_TO_ADD', data: null});
  }

  return (
      <Dialog
        TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="sm"
        open={StockToAdd !== null}
        onClose={() => close()}
        aria-labelledby="max-width-dialog-title"
      >
        <DialogTitle id="max-width-dialog-title" 
          style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor:'#F5F5F5',
           }}
        >
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', cursor: 'default'}}>
            <div style={{fontSize: '18px', fontWeight:'bold'}}>
              {title}
              {StockToAdd && `: ${StockToAdd.name}`}
            </div>
            <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => close()} />
          </div>
        </DialogTitle>
        <DialogContent style={{padding: '0px', margin: '0px'}}>
          <div style={{minHeight: '200px', padding: '4px 10px'}}>
            <Header>Please select a group</Header>
            <div style={{border: '1px solid #E0E0E0', maxHeight:'300px', minHeight:'100px', overflowY: 'scroll', overflowX: 'hidden'}}>
              {groups.map((item, index) => {
                return ((item.description && item.description.trim() !== '') ?
                  <Tooltip placement="bottom" tooltip={item.description}>
                    <GroupRow onClick={() => setSelectedGroup(item)}>
                      <div style={{width: '30px', height: '20px'}}>
                        {selectedGroup && selectedGroup.id === item.id && <CheckIcon style={{color: 'green', width: '20px', height: '20px', margin: 'auto'}} />}
                      </div>
                      <div>
                        {item.name}
                      </div>
                    </GroupRow>
                  </Tooltip>
                  :
                  <GroupRow onClick={() => setSelectedGroup(item)}>
                    <div style={{width: '30px', height: '20px'}}>
                      {selectedGroup && selectedGroup.id === item.id && <CheckIcon style={{color: 'green', width: '20px', height: '20px', margin: 'auto'}} />}
                    </div>
                    <div>
                      {item.name}
                    </div>
                  </GroupRow>
                )
              })}  
            </div>             
          </div>

          <div style={{position:'sticky', bottom: '0',
                            padding: '10px 30px 6px 0px',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'flex-end',
                            backgroundColor: '#FCFCFC',
                            borderTop: '1px solid #EEEEEE',
                          }}
                    > 
            <StyledButton disabled={selectedGroup === null}
              style={{ marginRight:'10px', 
                      backgroundColor: selectedGroup === null ? '#AAAAAA' : '#3C9F40', 
                      color: '#FFFFFF', 
                      fontSize: '14px',
                      padding: '8px 24px 8px 24px',
                      marginBottom: '2px',
                }}
                onClick={Add}
            >
              Add
            </StyledButton>
            <StyledButton 
              type="button"
              style={{ marginLeft:'10px', 
                      backgroundColor: '#E0E0E0', 
                      color: '#000000', 
                      fontSize: '14px',
                      padding: '8px 16px 8px 16px',
                      marginBottom: '2px',
                }}
                onClick={() => close()}
            >
              Cancel
            </StyledButton>
          </div>
        </DialogContent>
      </Dialog>
  );
}

export default AddStockToGroupDialog;
