import React, { useEffect, useState, useContext } from "react";
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import { Button } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import * as signalR from "@microsoft/signalr";
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const OneProgressComponent = (props) => {
  const { CurrentUser } = useContext(AppContext);
  const { item } = props;
  const [progress, setProgress] = useState(0);
  const [state, setState] = useState('');

  useEffect(() => {
    const hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(`messagehub`)
      .withAutomaticReconnect()
      .build();

    hubConnection.start().then(() => {
      hubConnection.on('cnnProgress', (name, message) => {
        if (item.name === name) {
          setProgress(parseInt(message));
        }
      });
      hubConnection.on('cnnState', (name, message) => {
        if (item.name === name) {
          setState(message);
        }
      });
    }).catch(err => { });  

    return () => {}
  }, [item.name]);


  return (
      <div style={{marginTop:'10px'}}>
        <div style={{display:'flex', alignItems:'center'}}>
          <div style={{width: '100px'}}>{item.name}</div>        
          <div style={{width: '280px'}}>{item.description}</div> 
          <div style={{width: '220px'}}>{item.lastUpdate}</div>
          <div style={{width: '220px'}}>{item.lastStart}</div>
          <div style={{width: '220px'}}>{item.lastEnd}</div>
          <div style={{width: '220px'}}>{item.failed}</div>
          <div style={{width: '100px'}}>{item.count}</div>  
          <div style={{flexBasis: '50px', flexGrow: 1}}>
            <Button variant="contained" color="primary" style={{marginRight:'20px'}}
              onClick={() => StockService.updateCnn(`${item.name}`, CurrentUser.token)}>
              Update
            </Button>    
          </div>
          <div style={{flexBasis: '50px', flexGrow: 1}}>
            <Button variant="contained" color="secondary"
              onClick={() =>StockService.stopUpdateCnn(`${item.name}`, CurrentUser.token)}>
              Cancel
            </Button>
          </div>
        </div>
        <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={progress} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
          </Box>
        </Box>  
        <div style={{display:'flex', alignItems:'center'}}>
            {state}
        </div>                
      </div>
  );
};
 
export default OneProgressComponent