import React, { useState, useEffect, useContext } from "react";
import styled from 'styled-components';
import Pagination from '../common/pagination/index';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';
import StockTable from '../common/stockTable/index';

const Container = styled.div`
background-color: #E0E0E0;
padding: 10px 20px 30px 20px;
margin: auto;
min-height: 600px;
`;

const TitleBar = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
@media only screen and (max-width: 1024px) {
  flex-direction: column;
  align-items: flex-start;
} 
`;

const Title = styled.div`
  font-size: 32px;
  font-weight: bold;
  color: #C85050;
`;

const SmallTitle = styled.div`
  font-size: 16px;
  font-weight: bold;
  color: #0073A6;
  margin-left: 10px;
  margin-bottom: 4px;

  @media only screen and (max-width: 1200px) {
    visibility: collapse;
    width: 0px;
    height: 0px;
  }  
`;

const defaultPageSize = 20;

const GroupTitle = {
  dow30: "Dow Jones 30",
  sp500: "S&P 500", 
}

const StockGroupComponent = (props) => {  
  const { dispatch } = useContext(AppContext);
  const groupName = props.paras.p || '';

  const [state, setState] = useState({
    totalPage: 30,
    currentPage: 1,
    pageSize: defaultPageSize,
    totalItems: 0,
    data: null,
    sortMethod: 1,
    exchangeText: '',
    loading: false
  });

  const { totalPage, currentPage, pageSize, totalItems, data, sortMethod, exchangeText, loading } = state;

  useEffect(() => {
    changePageSize(defaultPageSize);
    window.scrollTo(0, 0)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [groupName]);

  const changePageSize = (size) => {
    setState((prevState) => ({...prevState, loading: true}));
    StockService.loadStockGroup(groupName, 0, size, sortMethod).then(res => { 
      let count = Math.floor(res.data.total / size);
      if ((res.data.total % size) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        pageSize: size,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  const changePage = (num) => {
    setState((prevState) => ({...prevState, loading: true}));
    let start = (num - 1) * pageSize;
    StockService.loadStockGroup(groupName, start, pageSize, sortMethod).then(res => {
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: num,
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    })
  }

  const changeSort = (method) => {
    setState((prevState) => ({...prevState, loading: true}));

    StockService.loadStockGroup(groupName, 0, pageSize, method).then(res => { 
      let count = Math.floor(res.data.total / pageSize);
      if ((res.data.total % pageSize) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        sortMethod: method,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  return (
    <Container>
      <TitleBar>
        <div style={{display: 'flex', alignItems: 'flex-end'}}>
          <Title><b>{GroupTitle[groupName]}</b></Title>
          <SmallTitle>{exchangeText} ({totalItems} stocks) </SmallTitle>
        </div>
        <div style={{paddingTop:'10px'}}>
          <Pagination totalPages={totalPage}
                      currentPage={currentPage}
                      currentPageSize={pageSize}
                      pageSizes={[10, 20, 30, 50, 100]}
                      onPageChanged = {(num) => changePage(num)}
                      onPageSizeChanged = {(size) => changePageSize(size)}
                      />
        </div>
      </TitleBar>

      <StockTable data={data} sortMethod={sortMethod} loading={loading} changeSort={changeSort} />

      {data && data.length > 20 &&
      <Pagination totalPages={totalPage}
                  currentPage={currentPage}
                  currentPageSize={pageSize}
                  pageSizes={[10, 20, 30, 50, 100]}
                  onPageChanged = {(num) => changePage(num)}
                  onPageSizeChanged = {(size) => changePageSize(size)}
                  />  
      }

    </Container>
  )
}

export default StockGroupComponent;