import React from "react";
import styled from 'styled-components';
import YahooImage from '../common/dialog/webSiteLinks/images/yahoo.png';
import ZackImage from '../common/dialog/webSiteLinks/images/Zack.png';
import StockChartsImage from '../common/dialog/webSiteLinks/images/stockCharts.png';
import BarChartImage from '../common/dialog/webSiteLinks/images/barChart.png';
import FinvizImage from '../common/dialog/webSiteLinks/images/finviz.png';
import MartketWatchImage from '../common/dialog/webSiteLinks/images/marketWatch.png';
import TradingViewImage from '../common/dialog/webSiteLinks/images/tradingView.png';

const StyledContainer = styled.div`
  background-color: #ffffff;
  padding: 10px 0px 30px 0px;
`;

const StyledSubContainer = styled.div`
  width: 80%;
  margin: auto;

  @media (max-width: 700px) {
    width: 100%;
    padding: 10px;
  }  
`;

const StockLinks = [ 
  {
    image: TradingViewImage,
    title: 'TradingView',
  },
  {
    image: YahooImage,
    title: 'Yahoo finance',
  },
  {
    image: ZackImage,
    title: 'Zacks',
  },
  {
    image: StockChartsImage,
    title: 'StockCharts',
  },
  {
    image: BarChartImage,
    title: 'BarChart',
  },
  {
    image: FinvizImage,
    title: 'Finviz',
  },  
  {
    image: MartketWatchImage,
    title: 'MarketWatch',
  },  
];

const LinkContent = styled.div`
display: flex;
flex-direction: column;
align-items: center;
padding: 15px;
`;

const StyledTitle = styled.h2`
  font-size: 42px;
  font-weight: 400px;
  text-align: center;
  margin: 30px 0px 0px 0px;
`;

const StyledSubTitle = styled.div`
  font-size: 20px;
  font-weight: 300px;
  text-align: center;
  margin: 20px 0px;
`;

const StyledIconContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 20px;

  @media (max-width: 900px) {
    flex-wrap: wrap;
  }  
`;

const SectionC = (props) => {
    return (
      <StyledContainer>
        <StyledSubContainer>
            <StyledTitle>
              Stock Chart
            </StyledTitle>
            <StyledSubTitle>
              Provides the best market chart links to let you select your favorite stock chart
              </StyledSubTitle>
            <StyledIconContainer>
            {StockLinks.map((link, index) => {
              return (
                    <LinkContent>
                      <img src={link.image} width={80} height={80} alt='icon' />
                      <span style={{marginTop:'6px', fontWeight:'bold'}}>{link.title}</span>
                    </LinkContent>
              )})
            }
            </StyledIconContainer>
        </StyledSubContainer>
      </StyledContainer>
    );
}

export default SectionC;

