import React, { useState, useEffect, useContext } from "react";
import styled from 'styled-components';
import FilterIcon from '@material-ui/icons/Filter';
import TimelineIcon from '@material-ui/icons/Timeline';
import FormatListBulletedIcon from '@material-ui/icons/FormatListBulleted';
import Pagination from '../common/pagination/index';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';
import StockTable from '../common/stockTable/index';
import StockChartGroup from '../common/stockChart/index';
import FilterDialog from './filterDialog';
import Constants from './constants';

const Container = styled.div`
background-color: #E0E0E0;
padding: 10px 20px 30px 20px;
margin: auto;
min-height: 600px;
`;

const TitleBar = styled.div`
display: flex;
justify-content: space-between;
align-items: center;
@media only screen and (max-width: 1024px) {
  flex-direction: column;
  align-items: flex-start;
} 
`;

const Title = styled.div`
  font-size: 32px;
  font-weight: bold;
  color: #C85050;
`;

const SmallTitle = styled.div`
  font-size: 16px;
  font-weight: bold;
  color: #0073A6;
  margin-left: 10px;
  margin-bottom: 4px;

  @media only screen and (max-width: 1200px) {
    visibility: collapse;
    width: 0px;
    height: 0px;
  }  
`;

const defaultPageSize = 20;

const StockTableComponent = (props) => {  
  const { CurrentUser, dispatch } = useContext(AppContext);
  const exchangeSite = props.paras.p || 'ALL';
 
  const [priceValue, setPriceValue] = useState(Constants.priceValue);
  const [changeValue, setChangeValue] = useState(Constants.changeValue);
  const [volumeValue, setVolumeValue] = useState(Constants.volumeValue);
  const [peValue, setPEValue] = useState(Constants.peValue);
  const [positionValue, setPositionValue] = useState(Constants.positionValue);
  const [dividendValue, setDividendValue] = useState(Constants.dividendValue);

  const [filterOpen, SetFilterOpen] = useState(false);
  const [state, setState] = useState({
    totalPage: 30,
    currentPage: 1,
    pageSize: defaultPageSize,
    totalItems: 0,
    data: null,
    sortMethod: 1,
    exchangeText: '',
    loading: false
  });

  const [showChart, setShowChart] = useState(false);

  const { totalPage, currentPage, pageSize, totalItems, data, sortMethod, exchangeText, loading } = state;

  useEffect(() => {
    changePageSize(defaultPageSize);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [exchangeSite]);


  
  const updateFilter = (prices, changes, volumes, pes, positions, dividends) => {
    setState((prevState) => ({...prevState, loading: true}));
    StockService.loadStock(0, pageSize, sortMethod, exchangeSite, prices, changes, volumes, pes, positions, dividends).then(res => { 
      let count = Math.floor(res.data.total / pageSize);
      if ((res.data.total % pageSize) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  const changePageSize = (size) => {
    setState((prevState) => ({...prevState, loading: true}));
    StockService.loadStock(0, size, sortMethod, exchangeSite, priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue).then(res => { 
      let count = Math.floor(res.data.total / size);
      if ((res.data.total % size) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        pageSize: size,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  const changePage = (num) => {
    setState((prevState) => ({...prevState, loading: true}));
    let start = (num - 1) * pageSize;
    StockService.loadStock(start, pageSize, sortMethod, exchangeSite, priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue).then(res => {
      let count = Math.floor(res.data.total / pageSize);
      if ((res.data.total % pageSize) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: num,
        totalPage: count,
        totalItems: res.data.total,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    })
  }

  const changeSort = (method) => {
    setState((prevState) => ({...prevState, loading: true}));

    StockService.loadStock(0, pageSize, method, exchangeSite, priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue).then(res => { 
      let count = Math.floor(res.data.total / pageSize);
      if ((res.data.total % pageSize) !== 0) {
        count++;
      }
      setState((prevState) => ({
        ...prevState,
        loading: false,
        data: res.data.stocks,
        currentPage: 1,
        totalPage: count,
        totalItems: res.data.total,
        sortMethod: method,
        exchangeText: res.data.description
      }));
    }, err => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: `Fail to load stock data`}});
    });
  }

  return (
    <Container>
      <TitleBar>
        <div style={{display: 'flex', alignItems: 'flex-end'}}>
          <Title>{exchangeSite === 'ALL' ? 'US Stocks' : exchangeSite}</Title>
          <SmallTitle>{exchangeSite === 'ALL' ? '' : exchangeText} ({totalItems} stocks) </SmallTitle>
          {CurrentUser && (CurrentUser.role === "Admin" || CurrentUser.role === "SuperUser") && 
          <span>
          <FilterIcon style={{color: '#3C9F40', width: '32px', height: '32px', marginLeft: '10px', marginRight: '10px', cursor: 'pointer'}}
               onClick={() => SetFilterOpen(true)} />
          <TimelineIcon style={{color: '#0073A6', width: '32px', height: '32px', marginLeft: '10px', marginRight: '10px', cursor: 'pointer'}}
               onClick={() => setShowChart(true)} />
          <FormatListBulletedIcon style={{color: '#0073A6', width: '32px', height: '32px', marginLeft: '10px', marginRight: '10px', cursor: 'pointer'}}
               onClick={() => setShowChart(false)} />
          </span>
          }
        </div>
        <div style={{paddingTop:'10px'}}>
          <Pagination totalPages={totalPage}
                      currentPage={currentPage}
                      currentPageSize={pageSize}
                      pageSizes={[10, 20, 30, 50, 100]}
                      onPageChanged = {(num) => changePage(num)}
                      onPageSizeChanged = {(size) => changePageSize(size)}
                      />
        </div>
      </TitleBar>

      {!showChart && <StockTable data={data} sortMethod={sortMethod} loading={loading} changeSort={changeSort} />}
      {showChart && <StockChartGroup data={data} loading={loading} />}

      {data && data.length > 20 &&
      <Pagination totalPages={totalPage}
                  currentPage={currentPage}
                  currentPageSize={pageSize}
                  pageSizes={[10, 20, 30, 50, 100]}
                  onPageChanged = {(num) => changePage(num)}
                  onPageSizeChanged = {(size) => changePageSize(size)}
                  />  
      }

      <FilterDialog open={filterOpen} close={() => SetFilterOpen(false)}
        priceValue={priceValue} changeValue={changeValue} volumeValue={volumeValue}
        peValue={peValue} positionValue={positionValue} dividendValue={dividendValue}
        onUpdateFilter={(priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue) => {
          setPriceValue(priceValue);
          setChangeValue(changeValue);
          setVolumeValue(volumeValue);
          setPEValue(peValue);
          setPositionValue(positionValue);
          setDividendValue(dividendValue);
          SetFilterOpen(false);

          updateFilter(priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue);
        }}
      />
    </Container>
  )
}

export default StockTableComponent;