import React from "react";
import styled from 'styled-components';
import StockTableImage from './stockTable.png';

const StyledContainer = styled.div`
  background-color: #eeeeee;
  padding: 10px 0px 30px 0px;
`;

const StyledSubContainer = styled.div`
  width: 60%;
  margin: auto;

  @media (max-width: 700px) {
    width: 100%;
    padding: 10px;
  }  
`;

const StyledTitle = styled.h2`
  font-size: 42px;
  font-weight: 400px;
  text-align: center;
  margin: 30px 0px 0px 0px;
`;

const StyledSubTitle = styled.div`
  font-size: 20px;
  font-weight: 300px;
  text-align: center;
  margin: 20px 0px;
`;

const SectionBComponent = (props) => {
    return (
      <StyledContainer>
        <StyledSubContainer>
            <StyledTitle>
              Stock Table
            </StyledTitle>
            <StyledSubTitle>
              Every column is sortable
            </StyledSubTitle>
            <div style={{display: 'flex', justifyContent: 'center', marginBottom: '30px'}}>
                <img src={StockTableImage} style={{width: '100%', height: 'auto'}} alt='Stock Table' />
            </div>
        </StyledSubContainer>
      </StyledContainer>
    );
}

export default SectionBComponent;

