import React from "react";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import StarIcon from '@material-ui/icons/Star';
import styled from 'styled-components';
import SectionA from './sectionA';
import SectionB from './sectionB';
import SectionC from './sectionC';
import SectionD from './sectionD';
import { withRouter } from 'react-router-dom';

const StyledContainer = styled.div`
  background-color: #eeeeee;
  padding: 10px 0px 30px 0px;
`;

const StyledIntroduction = styled.div`
  width: 80%;
  margin: auto;

  @media (max-width: 700px) {
    width: 100%;
    padding: 10px;
  }  
`;

const StyledTitle = styled.h2`
  font-size: 42px;
  font-weight: 700;
  text-align: center;
  margin: 30px 0px 0px 0px;
`;

const StyledSubTitle = styled.div`
  font-size: 20px;
  font-weight: 400;
  text-align: center;
  margin: 20px 0px;
`;

const StyledButton = styled.div`
  background-color: #008800;
  padding: 10px 30px;
  color: #ffffff;
  border-radius: 30px;
  cursor: pointer;
  font-size: 20px;
  transition: all 0.5s;

  &:hover {
    box-shadow: 5px 5px 5px #aaaaaa;
    padding: 15px 40px;
    font-size: 30px;
  }
`;

const HomeComponent = (props) => {
    return (
        <div>
          <StyledContainer>
            <StyledIntroduction>
              <StyledTitle>
                StockHub
              </StyledTitle>
              <StyledSubTitle>
                Help you make the best decision in your investment
              </StyledSubTitle>
              <div style={{display: 'flex', justifyContent: 'center'}}>
                <List component="nav" aria-label="main mailbox folders">
                  <ListItem button>
                    <ListItemIcon>
                      <StarIcon style={{color: '#C85050'}} />
                    </ListItemIcon>
                    <ListItemText primary="Lists all stocks in 10 US exchange centers" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <StarIcon style={{color: '#C85050'}} />
                    </ListItemIcon>
                    <ListItemText primary="Provides data in sortable table to help you quickly find your stock" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <StarIcon style={{color: '#C85050'}} />
                    </ListItemIcon>
                    <ListItemText primary="Links to many other stock websites to help you find more infomation" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <StarIcon style={{color: '#C85050'}} />
                    </ListItemIcon>
                    <ListItemText primary="Save your selections in groups for later analysis" />
                  </ListItem>
                  <ListItem button>
                    <ListItemIcon>
                      <StarIcon style={{color: '#C85050'}} />
                    </ListItemIcon>
                    <ListItemText primary="Data updated after market closed" />
                  </ListItem>
                </List>
              </div>
              <div style={{display: 'flex', justifyContent: 'center'}}>
                <StyledButton onClick={() => props.history.push('?c=login')}>
                  <span>It is free</span>
                </StyledButton>
              </div>


            </StyledIntroduction>
          </StyledContainer>
          <SectionA />
          <SectionB />
          <SectionC />
          <SectionD />
        </div>
    );
}

export default withRouter(HomeComponent);

