import React, { useContext, useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Zoom from '@material-ui/core/Zoom';
import StyledCloseButton from '../buttons/styledCloseButton';
import StyledButton from '../buttons/styledButton';
import { AppContext } from '../../../context/appContext';
import { RelatedLinks, GetLinkUrl } from './webSiteLinks/index';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const StockWebSiteDialog = () => {
  const { dispatch, StockSelected } = useContext(AppContext);
  const [currentLink, setCurrentLink] = useState(0);

  return (
  <Dialog
    open={StockSelected !== null}
    TransitionComponent={Transition}
    onClose={() => dispatch({type: 'SET_SELECTED_SYMBOL', data: null})}
    scroll={'paper'}
    fullWidth={true}
    maxWidth={'md'}
    aria-labelledby="scroll-dialog-title"
    aria-describedby="scroll-dialog-description"
  >
    <DialogTitle id="max-width-dialog-title" 
      style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor: '#F5F5F5' }}
    >
      <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
        <div style={{fontSize: '18px', fontWeight:'bold'}}>{StockSelected}</div>
        <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => dispatch({type: 'SET_SELECTED_SYMBOL', data: null})} />
      </div>
    </DialogTitle>
    <DialogContent style={{padding: '0px', margin: '0px'}}>
      <div style={{padding: '20px 40px'}}>
        <RelatedLinks currentLink={currentLink} 
          onLinkClick={(linkType) => {
            setCurrentLink(linkType)
          }}
          onLinkDbClick={(linkType) => { 
            setCurrentLink(linkType); 
            dispatch({type: 'SET_SELECTED_SYMBOL', data: null});
            dispatch({type: 'SET_STOCK_LINKTYPE', data: linkType});
            window.open(GetLinkUrl(linkType, StockSelected), "_Blank");
          }}
        />
      </div>

      <div style={{position:'sticky', bottom: '0',
          padding: '10px 30px 10px 0px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-end',
          backgroundColor: '#FCFCFC',
          borderTop: '1px solid #EEEEEE',
        }}
        > 
          <StyledButton 
            onClick={() => {
              dispatch({type: 'SET_SELECTED_SYMBOL', data: null});
              dispatch({type: 'SET_STOCK_LINKTYPE', data: currentLink});
              window.open(GetLinkUrl(currentLink, StockSelected), "_Blank");
             }}
            style={{ marginRight:'10px', 
                    backgroundColor: '#3C9F40', 
                    color: '#FFFFFF', 
                    fontSize: '14px',
                    padding: '8px 24px 8px 24px',
                    marginBottom: '2px',
              }}
          >
            Open
          </StyledButton>
          <StyledButton 
            onClick={() => dispatch({type: 'SET_SELECTED_SYMBOL', data: null})}
            style={{ marginLeft:'10px', 
                    backgroundColor: '#E0E0E0', 
                    color: '#000000', 
                    fontSize: '14px',
                    padding: '8px 16px 8px 16px',
                    marginBottom: '2px',
              }}
          >
            Close
          </StyledButton>
        </div>
    </DialogContent>
  </Dialog>
  )
}

export default StockWebSiteDialog;