import React, { useEffect, useState } from "react";
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { withStyles } from '@material-ui/core/styles';
import * as signalR from "@microsoft/signalr";

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const SectionProgressComponent = (props) => {
  const { start, end } = props;
  const [progress, setProgress] = useState(0);
  const [state, setState] = useState('');
  const name = `${start}-${end}`;

  useEffect(() => {
    const hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(`messagehub`)
      .withAutomaticReconnect()
      .build();

    hubConnection.start().then(() => {
      hubConnection.on('cnnAutoProgress', (_name, message) => {
        if (name === _name) {
          setProgress(parseInt(message));
        }
      });
      hubConnection.on('cnnAutoState', (_name, message) => {
        if (name === _name) {
          setState(message);
        }
      });
    }).catch(err => { });  

    return () => {}
  }, [name]);


  return (
      <div style={{marginTop:'10px'}}>
        <div style={{display:'flex', alignItems:'center'}}>
          <div>
            <span>{name}</span>
            <span style={{marginLeft: '20px'}}>{state}</span>
          </div>
        </div>
        <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={progress} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
          </Box>
        </Box>  
      </div>
  );
};
 
export default SectionProgressComponent