import React, { useState, useEffect } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import StyledButton from '../common/buttons/styledButton';
import StyledCloseButton from '../common/buttons/styledCloseButton';
import Zoom from '@material-ui/core/Zoom';
import TextInput from '../common/textInput';
import TextArea from '../common/textArea';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const AddGroupDialog = (props) => {
  const [ name, setName ] = useState(props.editMode ? props.item.name : '');
  const [ nameError, setNameError ] = useState(null);
  const [ description, setDescription ] = useState(props.editMode ? props.item.description : '');
  const [ descriptionError, setDescriptionError ] = useState(null);

  useEffect(() => {
    setName(props.editMode ? props.item.name : '');
  }, [props.item, props.editMode]);

  let initCommand = props.editMode ? 'Update' : 'Add';
  let id = props.editMode ? props.item.id : 0;

  const onNameChanged = (value) => {
    setNameError(null);
    setName(value);
  }

  const onDescriptionChanged = (value) => {
    setDescriptionError(null);
    setDescription(value);
  }

  const onSubmit = () => {
    const _name = name.trim();
    if (_name.length === 0) {
      setNameError("Name is required");
      return;
    }
 
    const _description = description.trim();
    props.onAddGroup({id, name: _name, description: _description}) 
  }

  return (
      <Dialog
        TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="sm"
        open={props.open}
        onClose={() => props.close()}
        aria-labelledby="max-width-dialog-title"
      >
        <DialogTitle id="max-width-dialog-title" 
          style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor:'#F5F5F5',
           }}
        >
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
            <div style={{fontSize: '18px', fontWeight:'bold'}}>{props.editMode ? 'Edit Group' : 'Add Group' }</div>
            <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => props.close()} />
          </div>
        </DialogTitle>
        <DialogContent style={{padding: '0px', margin: '0px'}}>
            <div style={{margin: '10px 20px', fontSize: '14px'}}>
              <TextInput name="name" label="Name" type="text" value={name} errorMessage={nameError} onChange={onNameChanged} />
              <TextArea name="description" label="Description" type="text" value={description} errorMessage={descriptionError} onChange={onDescriptionChanged} />
            </div> 
            <div style={{position:'sticky', bottom: '0',
                  padding: '10px 30px 6px 0px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'flex-end',
                  backgroundColor: '#FCFCFC',
                  borderTop: '1px solid #EEEEEE',
                }}
              > 
              <StyledButton 
                type="sumbit"
                style={{ marginRight:'10px', 
                        backgroundColor: '#3C9F40', 
                        color: '#FFFFFF', 
                        fontSize: '14px',
                        padding: '8px 24px 8px 24px',
                        marginBottom: '2px',
                  }}
                onClick={onSubmit}
              >
                {initCommand}
              </StyledButton>
              <StyledButton 
                type="button"
                style={{ marginLeft:'10px', 
                        backgroundColor: '#E0E0E0', 
                        color: '#000000', 
                        fontSize: '14px',
                        padding: '8px 16px 8px 16px',
                        marginBottom: '2px',
                  }}
                  onClick={() => props.close()}
              >
                Cancel
              </StyledButton>
            </div>
        </DialogContent>
      </Dialog>
  );
}

export default AddGroupDialog;
