import React, { useContext, useState, useEffect } from "react";
import { AppContext } from '../../context/appContext';
import Rotation from '../common/animation/rotation';
import * as signalR from "@microsoft/signalr";
import ServerUrl from "../../service/server";

const MyTestComponent = (props) => {
  const { count, CurrentUser, dispatch, StockToAdd, GroupForNewStock } = useContext(AppContext);
  const [message, setMessage] = useState('');
  const [connection, setConnection] = useState(null);

  const sendSignalRMessage = () => {
    if (connection) {
      connection.invoke("SendTestMessage", message);
    }
  }

  const addSuccessMessage = () => {
    dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: message}});
  }
  const addErrorMessage = () => {
    dispatch({type:'ADD_MESSAGE', data: {type: 'error', text: message}});
  }
  const addInfoMessage = () => {
    dispatch({type:'ADD_MESSAGE', data: {type: 'info', text: message}});
  }
  const addWarningMessage = () => {
    dispatch({type:'ADD_MESSAGE', data: {type: 'warning', text: message}});
  }

  useEffect(() => {
    const hubConnection = new signalR.HubConnectionBuilder()
      .withUrl(`${ServerUrl}messagehub`)
      .withAutomaticReconnect()
      .build();

    hubConnection.start().then(() => {
      dispatch({type:'ADD_MESSAGE', data: {type: 'success', text: "SignalR connected"}});
      setConnection(hubConnection);
      if (hubConnection.connectionId) {
        hubConnection.invoke("sendConnectionId", hubConnection.connectionId);
      }   
      hubConnection.on('onTestMessage', message => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'info', text: message}});
      });
    }).catch(err => { });  

    return () => {
      hubConnection.stop().then(() => {
        dispatch({type:'ADD_MESSAGE', data: {type: 'warning', text: "SignalR disconnected"}});
      });
    }
  }, [dispatch]);

  return (
    <div>
      <div style={{width: "90%", margin:"auto", paddingTop:'50px', overflow: 'hidden'}}>
        <Rotation>My rotation</Rotation>
        <input type="text" id="fname" name="fname" onChange={(e) => setMessage(e.target.value)} />
        <button onClick={sendSignalRMessage} >Send SignalR messgae</button>
      </div>

      <div style={{width: "90%", margin:"auto", paddingTop:'20px', overflow: 'hidden'}}>
        <button onClick={addSuccessMessage} >Send Sucess Message</button>
        <button onClick={addErrorMessage} >Send Error Message</button>
        <button onClick={addInfoMessage} >Send Info Message</button>
        <button onClick={addWarningMessage} >Send Warning Message</button>
        <button onClick={() => dispatch({type:'INC_COUNT'})} >Inc</button>
        <button onClick={() => dispatch({type:'DEC_COUNT'})} >Dec</button>
      </div>
          
      <div style={{width: "90%", margin:"auto", paddingTop:'10px', overflow: 'hidden'}}>
        <h3>Count: {count}</h3>
        <h3>StockToAdd : {StockToAdd && `${StockToAdd.id} ${StockToAdd.name}` } </h3>
        <h3>GroupForNewStock : {GroupForNewStock && `${GroupForNewStock.id} ${GroupForNewStock.name}` } </h3>
        <h3>User: {JSON.stringify(CurrentUser)}</h3>
        <h3>Token: {CurrentUser && CurrentUser.token}</h3>
      </div>

    </div>
  );
}

export default MyTestComponent;

