import React, { useState, useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import { StockService } from '../../service/stock';
import StockTable from '../common/stockTable/index';
import Styled from 'styled-components';

const Container = Styled.div`
  background-color: #E0E0E0;
  padding: 10px 20px 30px 20px;
  margin: auto;
  min-height: 600px;
`;

const Calendar = () => {
  const [date, setDate] = useState(new Date().toISOString().substr(0, 10));
  const [data, setData] = useState(null);
  const [dataDate, setDataDate] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    StockService.loadCalendar(date).then(res => {
      setData(res.data.stocks);
      setDataDate(res.data.date);
      setLoading(false);
    }, err => {
      setLoading(false);
    })
  }, [date])

  const onChange = (event) => {
    setDate(event.target.value);
  }

  return (
    <Container>
      <div style={{display: 'flex', alignItems: 'center', marginBottom: '4px'}}>
        <span style={{marginRight: '10px'}}>Select a date</span>
        <TextField onChange={onChange}
          id="date"
          type="date"
          defaultValue={date}
          sx={{ width: 220 }}
        />
        {dataDate && <span style={{marginLeft:'20px'}}>{dataDate}</span>}
      </div>
      {data && <StockTable data={data} loading={loading} />}
    </Container>
  )
}

export default Calendar;