import React from "react";
import LoadingComponent from '../../common/loading/index';
import StockChartComponent from "./stockChart";

const StockChartGroup = (props) => {  
  const {data, loading } = props;

  return (
    <div style={{overflowX: 'auto', minHeight:'200px'}}>
      <div style={{position: 'relative'}}>
        {loading && <LoadingComponent />}
      </div>
      {!loading && data && data.map((item, index) => {
          return <StockChartComponent key={index} item={item} />
        }) 
      }
    </div>
  )
}

export default StockChartGroup;