import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Zoom from '@material-ui/core/Zoom';
import StyledCloseButton from '../common/buttons/styledCloseButton';
import StyledButton from '../common/buttons/styledButton';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const DeleteGroupDialog = (props) => {
  const { item, open, close, onDelete } = props; 
  return (
    <Dialog
      open={open}
      onClose={() => close()}
      TransitionComponent={Transition}
      keepMounted
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="max-width-dialog-title" 
        style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor: '#F5F5F5' }}
      >
        <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
          <div style={{fontSize: '18px', fontWeight:'bold'}}>Delete Group: {item && item.name}</div>
          <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => close()} />
        </div>
      </DialogTitle>
      <DialogContent style={{padding: '0px', margin: '0px'}}>
        <div style={{padding: '20px 60px 20px 20px'}}>
          <div style={{fontSize:'20px', margin:'4px 0px 20px 0px'}}>Are you sure to delete this group?</div>
        </div>

        <div style={{position:'sticky', bottom: '0',
                          padding: '10px 30px 6px 0px',
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'flex-end',
                          backgroundColor: '#FCFCFC',
                          borderTop: '1px solid #EEEEEE',
                        }}
                      > 
          <StyledButton 
            onClick={() => onDelete()}
            style={{ marginRight:'10px', 
                    backgroundColor: '#DA0050', 
                    color: '#FFFFFF', 
                    fontSize: '14px',
                    padding: '8px 24px 8px 24px',
                    marginBottom: '2px',
              }}
          >
            Delete
          </StyledButton>
          <StyledButton 
            onClick={() => close()} 
            style={{ marginLeft:'10px', 
                    backgroundColor: '#E0E0E0', 
                    color: '#000000', 
                    fontSize: '14px',
                    padding: '8px 16px 8px 16px',
                    marginBottom: '2px',
              }}
          >
            Cancel
          </StyledButton>
        </div>
      </DialogContent>
    </Dialog>
  );
}

export default DeleteGroupDialog;