import React, { useContext, Fragment } from "react";
import Header from '../headerFooter';
import Footer from '../headerFooter/footer';
import HomeComponent from '../home';
import StockTableComponent from '../stock/index';
import StockGroupComponent from '../stock/stockGroup';
import MyCollection from '../myCollection/index';
import CheckProgressComponent from '../dataManage/checkProgress';
import CheckFullProgressComponent from '../dataManage/checkFullProgress';
import UpdateSymbolComponent from '../dataManage/updateSymbol';
import UpdatePEComponent from '../dataManage/updatePE';
import UpdateBasicComponent from '../dataManage/updateBasic';
import UpdateCnnComponent from '../dataManage/cnnUpdate';
import UpdateWithCnnComponent from '../dataManage/updateDailyInfo';
import UpdateGroupComponent from '../dataManage/updateGroup';
import { AppContext } from '../../context/appContext';
import SignInComponent from '../auth/signin';
import ForgetPasswordComponent from '../auth/forgetpassword';
import ResetPasswordComponent from '../auth/resetpassword';
import ChangePasswordComponent from '../auth/changepassword';
import SignUpComponent from '../auth/signup';
import { withRouter } from 'react-router-dom';
import { AuthService } from '../../service/auth';
import MyTestComponent from '../test/myTest';
import CalendarComponent from '../calendar';

const AuthLinks = [
  "mycollection", "updatesymbol", "updatepe", "updatebasic", "updatecnn", "updategroup", "changepassword", "checkprogress", "checkfullprogress", "mytest",
];

const ContentTemplate = (props) => {
  const { CurrentUser, dispatch } = useContext(AppContext);

  const templates = {
      home: HomeComponent,
      stock: StockTableComponent,
      stockgroup: StockGroupComponent,
      mytest: MyTestComponent,
      mycollection: MyCollection,
      checkprogress: CheckProgressComponent,
      checkfullprogress: CheckFullProgressComponent,
      updatesymbol: UpdateSymbolComponent,
      updatepe: UpdatePEComponent,
      updatebasic: UpdateBasicComponent,
      updatecnn: UpdateCnnComponent,
      updatewithcnn: UpdateWithCnnComponent,
      updategroup: UpdateGroupComponent,
      calendar: CalendarComponent,
      login: SignInComponent,
      forgetpassword: ForgetPasswordComponent,
      resetpassword: ResetPasswordComponent,
      changepassword: ChangePasswordComponent,
      signup: SignUpComponent,
  };

  if (AuthLinks.includes(props.name.toLowerCase())) {
    if (!CurrentUser && !AuthService.getUserInfo()) {
      dispatch({type:'SAVED_PATH', data: props.history.location.search})
      props.history.push('?c=login')
      return (<div></div>);
    }
  } 

  const Template = (templates[props.name.toLowerCase()] || templates.home);

  return (
    <Fragment>
      <Header />
      <div style={{minHeight: '700px'}}>
        <Template {...props} />
      </div>
      <Footer />
    </Fragment>
  )
}

export default withRouter(ContentTemplate);