import React, { useEffect, useState, useContext } from "react";
import { Paper } from '@material-ui/core';
import { StockService } from '../../service/stock';
import Styled from 'styled-components';
import { AppContext } from '../../context/appContext';
import BasicProgress from './updateBasicProgress';

const TitleBar = Styled.div`
  display: flex;
  justify-content: space-between;
  padding: 8px;
  background-Color: #4CAF50;
  color: #FFFFFF;
  border-radius: 6px 6px 0px 0px;
`;

const UpdateBasicComponent = () => {
  const { CurrentUser } = useContext(AppContext);
  const [exchanges, setExchanges] = useState('');

  useEffect(() => {
    if (CurrentUser && CurrentUser.token) {
      StockService.getExchange(CurrentUser.token).then(res => {
        setExchanges(res.data);
      })  
    }
  }, [CurrentUser]);

  return (
    <Paper elevation={4} style={{borderRadius:'6px', width: '80%', margin:'20px auto'}}>
        <TitleBar>
          <span style={{fontSize:'20px', fontWeight:'bold'}}>Update Basic Data</span>
        </TitleBar>

          <div style={{padding:'10px'}}>
            <BasicProgress item={{name: 'all', description: 'All stocks', lastUpdate: '', count: ''}} />

            { exchanges && exchanges.map((item, index) => {
              return (
                  <BasicProgress key={index} item={item} />
                )
              })
            }
          </div>
    </Paper>
  );
};
 
export default UpdateBasicComponent;