import React, { useState, useContext, useEffect } from "react";
import { Paper, Divider } from '@material-ui/core';
import { InputLabel, MenuItem, FormControl, Select, Button } from "@material-ui/core";
import UploadIcon from '@material-ui/icons/CloudUpload';
import Styled from 'styled-components';
import { StockService } from '../../service/stock';
import { AppContext } from '../../context/appContext';

const Container = Styled.div`
padding: 20px;
`;

const UpdateSymbolComponent = () => {
  const { CurrentUser } = useContext(AppContext);
  const [uploadFile, setUploadFile] = useState(null);
  const [exchange, setExchange] = React.useState('');
  const [exchanges, setExchanges] = useState(null);

  useEffect(() => {
    if (CurrentUser) {
      StockService.getExchange(CurrentUser.token).then(res => {
        setExchanges(res.data);
      })
    }
  }, [CurrentUser]);

  const startUploadFile = (e) => {
    e.preventDefault();
    if (uploadFile === null || exchange === '' || !exchanges) return;

    const element = exchanges.find(x => x.name === exchange);
    if (!element) return;

    const formData = new FormData();
    formData.append('File', uploadFile);
    formData.append('Name', exchange);
    formData.append('Description', element.description);

    StockService.updateSymbol(formData, CurrentUser.token);
  }

  const onExchangeChanged = (event) => {
    setExchange(event.target.value);
  };

  return (
    <div style={{width: '80%', margin: '10px 10% 30px 10%'}}>
    <Paper elevation={4} style={{borderRadius:'10px', margin:'10px 0px'}}>
      <Container>
        <div style={{fontSize:'24px', fontWeight:'bold'}}>Update Symbols</div>
      </Container>
      <Divider />
      <Container>
        <form onSubmit={e => startUploadFile(e)}>
          <div style={{display:'flex', alignItems:'center', marginBottom:'10px'}}>
            <FormControl variant="outlined">
              <InputLabel>Exchange</InputLabel>
              {exchanges && (
                <Select
                  style={{ width: "300px" }}
                  id="demo-simple-select-outlined"
                  value={exchange}
                  onChange={onExchangeChanged}
                  label="Exchange"
                >
                  <MenuItem key={0} value="">Select an exchange</MenuItem>
                  { exchanges.map((item, index) => {
                    return (
                          <MenuItem key={index} value={item.name}>{item.description}</MenuItem>
                      )                              
                    })
                  }
                </Select>
                )
              }
            </FormControl>
            <div style={{marginLeft:'10px', minWidth: '80px'}}>{exchange}</div>
            <div style={{paddingLeft:'10px'}}>
              <input type="file" onChange={e => setUploadFile(e.target.files[0])} />
            </div>
            <Button type="submit"
              variant="contained"
              color="primary"
              startIcon={<UploadIcon />}
              style={{marginRight:'30px'}}
            >
              Upload
            </Button>
          </div>
        </form>
      </Container>
      <div style={{paddingLeft:'20px'}}>
            { exchanges && exchanges.map((item, index) => {
              return (
                  <div key={index} style={{margin:'10px 0px', borderBottom:'1px solid #eeeeee', paddingBottom: '10px'}}>
                    <div style={{display:'flex', alignItems:'center'}}>
                      <div style={{width: '100px'}}>{item.name}</div>        
                      <div style={{width: '280px'}}>{item.description}</div> 
                      <div style={{width: '220px'}}>{item.lastUpdate}</div>
                      <div style={{width: '100px'}}>{item.count}</div>
                      <Button variant="contained" color="primary" style={{marginRight:'20px'}}
                        onClick={() => window.open(`http://eoddata.com/Data/symbollist.aspx?e=${item.name}`)}>
                        Download
                      </Button>    
                      <Button variant="contained" color="secondary"
                        onClick={() =>StockService.stopUpdateSymbol(`${item.name}`)}>
                        Cancel
                      </Button>    
                    </div>
                  </div>
                )
              })
            }
          </div>
    </Paper>
    </div>
  );
};
 
export default UpdateSymbolComponent;