import React, { useEffect } from "react";
import * as d3 from "d3";

const HistoricalChartComponent = (props) => {  
  const myReference = React.createRef();
  const { data, width } = props;

  useEffect(() => {
    updateStock()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data, width])

  const updateStock = () => {
    if (data === null || width === 0) return;

    var margin = 40;
    var height = width / 3;

    d3.select(myReference.current).select("svg").remove();

    var dataGroup = d3.select(myReference.current).append("svg")
        .attr("width", width)
        .attr("height", height + margin + 10)
        .append("g")
        .attr("transform", "translate(" + margin + ", 10)");

    var line = d3.line()
        .x(d => x(d.index))
        .y(d => y(d.open));

    var line2 = d3.line()
        .x(d => x(d.index))
        .y(d => y2(d.volume));

    var x = d3.scaleLinear()
        .domain(d3.extent(data, function (d) { return d.index }))
        .range([0, width - margin])

    var y = d3.scaleLinear()
        .domain([0, d3.max(data, function (d) { return d.open })])
        .range([height, 0])
        .nice();

    var y2 = d3.scaleLinear()
        .domain([0, d3.max(data, function (d) { return d.volume })])
        .range([height, height * 0.75])

    dataGroup.append("path")
        .data([data])
        .attr("fill", "none")
        .attr("stroke", "black")
        .attr("d", line)

    dataGroup.append("path")
        .data([data])
        .attr("fill", "none")
        .attr("stroke", "red")
        .attr("d", line2)


    var xAxisGroup = dataGroup
        .append("g")
        .attr("class", "xAxisGroup")
        .attr("transform", "translate(0," + height + ")")

    var xAxis = d3.axisBottom(x);

    xAxis(xAxisGroup);

    var yAxisGroup = dataGroup
        .append("g")
        .attr("class", "yAxisGroup")

    var yAxis = d3.axisLeft(y);

    yAxis(yAxisGroup);    
  }

  return (
      <div ref={myReference} />
  )
}

export default HistoricalChartComponent;