import React, { useState, useEffect } from "react";
import * as signalR from "@microsoft/signalr";
import axios from 'axios';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import UploadIcon from '@material-ui/icons/CloudUpload';
import CancelIcon from '@material-ui/icons/Cancel';
import Button from '@material-ui/core/Button';
import Styled from 'styled-components';
import { withStyles } from '@material-ui/core/styles';

const hubUrl = 'https://localhost:44358/messagehub';
const stopUrl = 'https://localhost:44358/DataManage/stopUpdatePE';
const updateUrl = 'https://localhost:44358/DataManage/UpdatePE';

const Container = Styled.div`
width: 100%;
margin: auto;
margin-top: 20px;
margin-bottom: 20px;
background-color: #f0f0f0;
padding: 20px;
border-radius: 10px;
`;

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 20,
  },
  colorPrimary: {
    backgroundColor: '#d8d8d8',
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#008000',
  },
}))(LinearProgress);

const UpdatePEComponent = () => {
  const [showProgress, setShowProgress] = useState(false);

  const hubConnection = new signalR.HubConnectionBuilder()
  .withUrl(hubUrl)
  .withAutomaticReconnect()
  .build();
 
  hubConnection.start().then(a => {
    if (hubConnection.connectionId) {
      hubConnection.invoke("sendConnectionId", hubConnection.connectionId);
    }   
  }).catch(err => {

  });  

    const SignalRClient = () => {
      const [progress, setProgress] = useState(0);
      const [message, setMessage] = useState(null);
 
      useEffect(() => {
        hubConnection.on("updatePEProgress", (value) => {
          setProgress(value);
        });
        hubConnection.on("updatePEMessage", (value) => {
          setMessage(value);
        });
      });
 
      return (
        <div style={{marginTop:'10px'}}>
          <div>
            <span>{message}</span>
          </div>
          <Box display="flex" alignItems="center" style={{marginTop:'10px'}}>
            <Box width="100%" mr={1}>
              <BorderLinearProgress variant="determinate" value={progress} />
            </Box>
            <Box minWidth={35}>
              <Typography variant="body2" color="textSecondary">{`${progress}%`}</Typography>
            </Box>
          </Box>                  
        </div>
      );
    };

    const stopUpdating = () => {
      axios.get(stopUrl);
      setShowProgress(false);
    }

    const startUpdatePE = (e) => {
      e.preventDefault();

      setShowProgress(true);
      axios.get(updateUrl)
    }

    return (
      <Container>
          <div style={{fontSize:'24px', fontWeight:'bold', marginBottom:'20px'}}>Update PE</div>
          <Button type="submit"
            variant="contained"
            color="primary"
            startIcon={<UploadIcon />}
            style={{marginRight:'30px'}}
            onClick={startUpdatePE}
          >
            Update
          </Button>
          <Button
            variant="contained"
            color="secondary"
            startIcon={<CancelIcon />}
            onClick={stopUpdating}
          >
            Cancel
          </Button>
        {showProgress && <SignalRClient />}
      </Container>
    );
};
 
export default UpdatePEComponent;