import React, { useState, useEffect } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Zoom from '@material-ui/core/Zoom';
import Styled from 'styled-components';
import StyledButton from '../common/buttons/styledButton';
import StyledCloseButton from '../common/buttons/styledCloseButton';
import RangeSlider from './rangeSlider';
import Constants from './constants';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Zoom ref={ref} {...props} />;
});

const DarkColor = '#e8e8e8';
const LightColor = '#ffffff';

const Container = Styled.div`
  padding: 4px 30px;
  font-size: 14px;
`;

const FilterDialog = (props) => {
  const [priceValue, setPriceValue] = useState(props.priceValue);
  const [changeValue, setChangeValue] = useState(props.changeValue);
  const [volumeValue, setVolumeValue] = useState(props.volumeValue);
  const [peValue, setPEValue] = useState(props.peValue);
  const [positionValue, setPositionValue] = useState(props.positionValue);
  const [dividendValue, setDividendValue] = useState(props.dividendValue);

  useEffect(() => {
  }, []);

  const onReset = () => {
    setPriceValue(Constants.priceValue);
    setChangeValue(Constants.changeValue);
    setVolumeValue(Constants.volumeValue);
    setPEValue(Constants.peValue);
    setPositionValue(Constants.positionValue);
    setDividendValue(Constants.dividendValue);
  }

  return (
      <Dialog
        TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="sm"
        open={props.open}
        onClose={() => props.close()}
        aria-labelledby="max-width-dialog-title"
      >
        <DialogTitle id="max-width-dialog-title" 
          style={{ borderBottom:'1px solid #DDDDDD', padding: '6px 16px', backgroundColor:'#F5F5F5',
           }}
        >
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
            <div style={{fontSize: '18px', fontWeight:'bold'}}>Filter</div>
            <StyledCloseButton style={{ width: '20px', height: '20px'}} onClick={() => props.close()} />
          </div>
        </DialogTitle>
        <DialogContent style={{padding: '0px', margin: '0px'}}>
         {/* <div>{priceValue[0]} {priceValue[1]} {changeValue[0]} {changeValue[1]} {volumeValue[0]} {volumeValue[1]} 
         {peValue[0]} {peValue[1]} {positionValue[0]} {positionValue[1]} {dividendValue[0]} {dividendValue[1]}</div> */}
 
          <Container style={{backgroundColor: DarkColor}}>
            <RangeSlider min={Constants.priceValue[0]} max={Constants.priceValue[1]} step={1} start={priceValue[0]} end={priceValue[1]} label="Price" onChange={(value) => setPriceValue(value)} />
          </Container>
          <Container style={{backgroundColor: LightColor}}>
            <RangeSlider min={Constants.changeValue[0]} max={Constants.changeValue[1]} step={1} start={changeValue[0]} end={changeValue[1]} label="Change" onChange={(value) => setChangeValue(value)} />
          </Container>
          <Container style={{backgroundColor: DarkColor}}>
            <RangeSlider min={Constants.volumeValue[0]} max={Constants.volumeValue[1]} step={0.1} start={volumeValue[0]} end={volumeValue[1]} label="Volume" onChange={(value) => setVolumeValue(value)} />
          </Container>
          <Container style={{backgroundColor: LightColor}}>
            <RangeSlider min={0} max={1000} step={1} start={peValue[0]} end={peValue[1]} label="PE" onChange={(value) => setPEValue(value)} />
          </Container>
          <Container style={{backgroundColor: DarkColor}}>
            <RangeSlider min={0} max={1} step={0.1} start={positionValue[0]} end={positionValue[1]} label="Position" onChange={(value) => setPositionValue(value)} />
          </Container>
          <Container style={{backgroundColor: LightColor}}>
            <RangeSlider min={0} max={100} step={1} start={dividendValue[0]} end={dividendValue[1]} label="Dividend" onChange={(value) => setDividendValue(value)} />
          </Container>

            <div style={{position:'sticky', bottom: '0',
                  padding: '10px 30px 6px 0px',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  backgroundColor: '#FCFCFC',
                  borderTop: '1px solid #EEEEEE',
                }}
              > 
              <div style={{paddingLeft: '20px'}}>
                <StyledButton 
                  type="sumbit"
                  style={{ marginRight:'10px', 
                          backgroundColor: '#1976d2', 
                          color: '#FFFFFF', 
                          fontSize: '14px',
                          padding: '8px 24px 8px 24px',
                          marginBottom: '2px',
                    }}
                  onClick={onReset}
                >
                  Reset
                </StyledButton>                              
              </div>

              <div style={{display: 'flex', justifyContent: 'flex-end', alignItems: 'center'}}>
                <StyledButton 
                  type="sumbit"
                  style={{ marginRight:'10px', 
                          backgroundColor: '#3C9F40', 
                          color: '#FFFFFF', 
                          fontSize: '14px',
                          padding: '8px 24px 8px 24px',
                          marginBottom: '2px',
                    }}
                  onClick={() => {props.onUpdateFilter(priceValue, changeValue, volumeValue, peValue, positionValue, dividendValue)}}
                >
                  Ok
                </StyledButton>
                <StyledButton 
                  type="button"
                  style={{ marginLeft:'10px', 
                          backgroundColor: '#E0E0E0', 
                          color: '#000000', 
                          fontSize: '14px',
                          padding: '8px 16px 8px 16px',
                          marginBottom: '2px',
                    }}
                    onClick={() => props.close()}
                >
                  Cancel
                </StyledButton>
              </div>
            </div>
        </DialogContent>
      </Dialog>
  );
}

export default FilterDialog;
