import React from "react";
import LoadingComponent from '../../common/loading/index';
import { StyledTable } from './styled';
import TableHeader from './tableHeader.js';
import TableRow from './tableRow';

const StockTableComponent = (props) => {  
  const {data, sortMethod, loading, changeSort, groupId } = props;

  return (
    <div style={{overflowX: 'auto', minHeight:'200px'}}>
      <div style={{position: 'relative'}}>
        {loading && <LoadingComponent />}
      </div>        
      <StyledTable>
        <TableHeader sortMethod={sortMethod} changeSort={(method) => changeSort(method)} />
        { data && data.map((item, index) => {
          return <TableRow key={index} item={item} groupId={groupId}/>
        }) 
      }
      </StyledTable>
    </div>
  )
}

export default StockTableComponent;